import React, { useRef, useState } from "react";
import * as S from "./styles.js"
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import {useHistory, useLocation} from "react-router-dom"
import background from '../../assets/logoPalcoweb.png';

export default function SpectaclesCarousel({spectacles}) {

  const history = useHistory();

  var scrollPerClick = 300;
  var scrollAmount = 0;

  const slider = useRef(null)

  function handleNext() {

   if (scrollAmount <= (slider.current.scrollWidth - slider.current.clientWidth)) {

    slider.current.scrollTo({
      top: 0,
      left: (scrollAmount += scrollPerClick),
      behavior: "smooth"
    })
   }

  }

  function handlePrevious() {

    slider.current.scrollTo({
      top: 0,
      left: (scrollAmount -= scrollPerClick),
      behavior: "smooth"
    })

    if (scrollAmount < 0) {
      scrollAmount = 0;
    }

  }

  return (
    <>
      <S.SectionContainer>

        <S.NavPrev onClick={() => handlePrevious()}>
          <NavigateBeforeIcon
            style={{fontSize: 50}}
          ></NavigateBeforeIcon>
        </S.NavPrev>

        <S.NavNext onClick={() => handleNext()}>
          <NavigateNextIcon
            style={{fontSize: 50}}
          ></NavigateNextIcon>
        </S.NavNext>

        <S.ListRow
          ref={slider}
        >
          {spectacles.length > 0 &&
          spectacles.map((item, key) => {

            return (
              <>
              {item.name === "cardVazio" ?
                <S.ListItem src={background} key={key}>
                </S.ListItem>
              :
                <S.ListItem src={item.thumbnail} key={key}>
                  <S.InfoCard>
                    <S.ButtonAcessar
                      className="btn btn-warning"
                      onClick={(i) => {
                        history.push(`/cardezao/${item.id}`);
                      }}
                    >
                      Trailer
                    </S.ButtonAcessar>
                  </S.InfoCard>
                </S.ListItem>
              }
              </>
            )
          })}
        </S.ListRow>
      </S.SectionContainer>
    </>
  );
}