import styled from 'styled-components';

export const SectionContainer = styled.div`
  position: relative;
  width: 100%;
  height: 170px;
  margin: 30px 5px;

  @media (min-width: 900px){
    height: 250px;
  }
`;

export const ListRow = styled.div`
  height: 170px;
  width: auto;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  text-align: center;
  padding-bottom: 10px !important;
  display: flex;
  align-items: center;

  @media (min-width: 900px){
    height: 250px;
  }
`;

export const ListItem = styled.div`
  min-width: 147px;
  max-width: 147px;
  height: 147px;
  border-radius: 50%;
  background-image: url(${(props) => props.src});
  background-size: cover;
  background-position: center;
  background-color: gray;
  cursor: pointer;
  transition: 0.5s ease;
  z-index: 2;

  transform: scale(0.9);

  &:hover {
    transform: scale(1);
    z-index: 5;
  }

  @media (min-width: 900px){
    min-width: 250px;
    max-width: 250px;
    height: 250px;
  }
`;

export const InfoCard = styled.div`
  width: 147px;
  height: 147px;
  display: flex;
  border-radius: 50%;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  z-index: 10;

  font-size: 12px;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;

  :hover {
    opacity: 1;
  }

  @media (min-width: 900px){
      width: 250px;
      height: 250px;
    }
`;

export const NavPrev = styled.div`
    position: absolute;
    width: 40px;
    height: 160px;
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;

    @media (min-width: 900px){
      height: 250px;
    }
`;

export const NavNext = styled(NavPrev)`
    right: 0;
    left: auto;
`;


export const Coracao = styled.span`
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 10px;
  color: ${(props) => (props.channelIsInFavorites ? "#FDCC12" : "")};
  :hover {
    color: #fdcc12;
  }
`;
export const CoracaoFav = styled.span`
  width: 30px;
  height: 30px;
  cursor: pointer;
  border-radius: 10px;
  color: #fdcc12;
  :hover {
    color: #fff;
  }
`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
`;

export const verMais = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
  cursor: pointer;
  :hover {
    color: #fdcc12;
  }
`;

