import styled from "styled-components";
import InputMask from "react-input-mask";

export const Input = styled(InputMask)`
  width: ${(props) => (props.size ? props.size + "px" : "100%")};
  margin: 0 !important;

`;

export const Row = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  margin: 0px;
`;