import React, { useEffect } from "react";
import { useHistory } from "react-router";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import * as S from "./styles";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";

function Canais() {
  const history = useHistory();
  const { getAllChannels, channels, channelLike } = useShows();
  const { userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";

  useEffect(() => {
    getAllChannels();
  }, []);
  console.log(channels);

  return (
    <>
      <Header />
      <S.Container>
        <Section title="Canais" filter>
          <S.GridContainer id="canais">
            {channels.map((card) => {
              channels.sort((card) => {
                if (card.fullName === "Tv PalcoWeb") return -1;
              });
              return (
                <S.CardCanais>
                  <S.Img
                    src={card.thumbnail}
                    style={{
                      position: "absolute",
                      width: 250,
                      height: 250,
                      borderRadius: "50%",
                    }}
                    value={card.id}
                    alt="imagem referente ao canal"
                  />
                  <S.InfoCard>
                    <S.Span size="18" style={{ marginBottom: "15px" }}>
                      {userIsAuthenticated() && (
                        <S.Coracao onClick={() => channelLike(userId, card.id)}>
                          <i className="bi bi-suit-heart"></i>
                        </S.Coracao>
                      )}
                    </S.Span>
                    <S.Span size="20">{card.fullName}</S.Span>

                    <S.Span size="10">
                      <i className="bi bi-circle-fill"></i>{" "}
                      <S.Span size="15">{card.category}</S.Span>{" "}
                    </S.Span>
                    <S.Span></S.Span>
                    <S.verMais
                      size="18"
                      style={{ marginTop: "15px" }}
                      onClick={() => {
                        if (card.key === 13) {
                          history.push("/canalpw");
                        } else {
                          history.push(`/canalpw`);
                        }
                      }}
                    >
                      Ver mais
                    </S.verMais>
                  </S.InfoCard>
                </S.CardCanais>
              );
            })}
          </S.GridContainer>
        </Section>
      </S.Container>

      <Footer />
    </>
  );
}

export default Canais;
