import styled from "styled-components";

import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top:100px;
  flex-direction: column;
  color: white;
`


export const Span = styled.span`
  height: 10vh;
  color: ${props => props.cor || 'white'};
  font-size: 1.5vw;
  font-size:${props => props.size + "rem"};
  text-align: center;
  @media (max-width: 769px) {
    font-size: 4.5vw;
    height: 5vh;
  }
`

export const  Sessao = styled.div`
  display: flex;
  flex-direction: column;
  width: 90vw;
  height: 35vh; 
  margin:10px ;
  padding: 1  0px;
  align-items: center;
  @media (max-width: 769px) {
    height: 30vh;
  }
`

export const  SessaoItem = styled.div`
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 10vh; 
  align-items: center;
  justify-content: space-around;
  @media (max-width: 769px) {
  height: 6vh;
  } 
`
export const  Dias = styled.div`
  border: 1px solid white;
  color: #E4B810;
  width: 20%;
  height: 100%;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  text-align:center;
`
export const  Horas = styled.div`
  border: 1px solid white;
  color: #E4B810;
  width: 25%;
  height: 100%;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  text-align:center;
`

export const  Minutos = styled.div`
  border: 1px solid white;
  color: #E4B810;
  width:30%;
  height: 100px;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  text-align:center;
`


export const Carrinho = styled.div`
display: flex;
width: 90vw;
height: 40vh;
flex-direction: row;
align-items: center;
justify-content: center;
@media (max-width: 769px) {
    height: 45vh;
    margin:0;
    padding:0;
  }
`

export const CarrinhoItem = styled.div`
  display: flex;
  height: auto;
  width: 10%;
  
  flex-direction: column;
`

export const Img = styled.img`
  width: 20vw;
  height: auto;
  padding: 10px;
  @media (max-width: 769px) {
      width: 45vw;
      height:auto;
      padding: 1vw;
    }
`
export const X = styled.div`
    display: flex;
    width: 2%;
    height: 150px;
   
`
export const Info = styled.div`
  display: flex;
  flex-direction: column;
  width: 30%;
  padding: 10px;
  @media (max-width: 769px) {
    width: 40vw;
    padding: 0;
    margin-left: 1vw;
  }
`
export const ButtonContinuar = styled.button`
background-color: #FDCC12;
color: black;
text-transform: uppercase;
font-weight: bolder;
:hover{
    background-color: #FDCC12;
}`
export const Button = styled.button`
`

export const BordarBottom = styled.div`
border-bottom: 1px solid white;
width: 50%;
height: 20px;
`

export const AreaCancelar  = styled.div`
display: flex;
justify-content: flex-end;
width: 50%;
padding-bottom:80px;
`

export const AreaButtons  = styled.div`
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding-bottom:80px;
  @media (max-width: 769px) {
    padding: 0;
    margin-bottom:2vh;
  }
`

export const ImgPlanos = styled.img`
  width: 125px;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  object-fit: cover;
  justify-content: center;
  height: 125px;
  border-radius: 10px;
`;

export const PopapDivButton = styled.div`
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background-color: #fdcc12;

  @media (max-width: 550px){
    width: 100%;
  }
`;

export const PopapButton = styled.button`
  width: 25vh;
  height: 5vh;
  background-color: #000;
  border-radius: 15px;
  border: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 10vh;

  :hover {
    padding: 10px;
  }
  @media (max-width: 550px){
    margin-bottom: 2vh;
  }
`;

export const Spann = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: ${(props) => props.size || "0.7rem"};

  text-align: justify;
`;

export const ContainerModal = styled.div`
  width: 100vw;
  background: black;
  display: flex;
  flex-direction: row;
  position: relative;
  background-color: #1d1d1d;
  margin-bottom: 30px;
  /* @media (max-width: ${SCREEN_SIZES.md}) {
    flex-direction: column;
  } */
  @media (max-width: ${SCREEN_SIZES.md}) {
    width: 100vw;

    flex-direction: column;
  }
`;

export const PopapDiv = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1b1b1b;
  margin-bottom:10vh;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 90vw;
    margin: 2vh auto auto auto;
  }
`;

export const PopapDiv1 = styled.div`
  width: 60%;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #fdcc12;
  border-radius: 15px;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 80%;
  }
`;