import React, { useEffect, useState } from "react";
import { useHistory, useParams } from "react-router-dom";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import { useShows } from "../../dados/context/ShowsContext";
import { usePlans } from "../../dados/context/PlansContext";
import * as S from "./styles";
import logoPW from "../../assets/logo-pw.png";
import Layout from "../../layout";


function ConfirmCompra() {
  const history = useHistory();

  const { adquiridos, getShowsByUser } = useShows();
  const { getPlan, plans } = usePlans();
  const routeStack = localStorage.getItem('stack:route');
  const { id } = useParams();

  const [ehVr, setEhVr] = useState(false);
  const [checked, setChecked] = useState(false);

  useEffect(() => {
    getShowsByUser();
  }, []);
  useEffect(() => {
    getPlan();
  }, []);
  //fazer um find e verificar na rota de filmes adquiridos se esse espetáculo está lá
  const espetaculoUsuario = adquiridos.find(
    (show) => show.id === id
  );
  const planoUsuario = plans.find(
    (e) => e.id === id
  );
  if(routeStack!='/pagamento'){
    history.push(routeStack)
  }

  function dataAtualFormatada(){
    var data = new Date(),
        dia  = data.getDate().toString(),
        diaF = (dia.length == 1) ? '0'+dia : dia,
        mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
        mesF = (mes.length == 1) ? '0'+mes : mes,
        anoF = data.getFullYear();
    return diaF+"/"+mesF+"/"+anoF;
}

function handleStyle () {
  const glow = document.getElementById('glow')
  glow.style.color = 'red' 
  glow.style.fontWeight = 'bold'
}

  return (
    <>
      {!ehVr && <>
      <Header />

      <S.Container>
          <S.Span size="1.3" className="p-5">
            Agradecemos sua compra
          </S.Span>
          {espetaculoUsuario ? 
            <S.Span className="p-2" cor="#E4B810">
              Você tem 24h para assistir esse espetáculo.
            </S.Span>
            : (<div></div>)
          } 
          
          <div style={{display: "flex", flexDirection: "column", alignItems:'center'}}>
        {espetaculoUsuario ? (
          <>
            
              <S.Img src={espetaculoUsuario.thumbnail}></S.Img>
           

            <S.Span className="pt-2" cor="#E4B810">
              {espetaculoUsuario.price_on_demand}
            </S.Span>
            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {dataAtualFormatada()}
            </S.Span>

            <S.Span cor="#E4B810">
              Total R$: {espetaculoUsuario.price_on_demand}
            </S.Span>
          </>
        ) :
        planoUsuario ?
        (
          <>
            {<S.ImgPlanos src={logoPW}></S.ImgPlanos>}
           
            <S.Span className="pt-2" cor="#E4B810">
              Plano {planoUsuario.name}

              {planoUsuario?.name?.includes("Anual") &&
              <>
                &nbsp;Promoção 3D 
              </>
              }
            </S.Span>

            {planoUsuario?.name?.includes("Anual") &&
          <>
             <S.Spann size="1.2" className="pt-2" cor="#E4B810">
             Seu óculos VR será enviado para o endereço cadastrado. 
            </S.Spann>
          </>
        }

            <S.Span className="pt-2" cor="#E4B810">
              Data da compra: {dataAtualFormatada()}
            </S.Span>
           
            <S.Span cor="#E4B810">
              Total R$: {planoUsuario.value.slice(0, -2)},{planoUsuario.value.slice(-2)}
            </S.Span>
          </>
        ) :
        (
          <div></div>
        )
        }
        </div>

        <S.AreaButtons>
          <S.Button className="btn" onClick={() => history.push(`/`)}>
            <S.Span size="1.0" cor="#FFF">
              {" "}
              <i class="bi bi-chevron-left"></i>Voltar
            </S.Span>
          </S.Button>
          <S.ButtonContinuar
            className="btn"
            onClick={() => planoUsuario ? history.push(`/espetaculos/assinante`) : history.push(`/espetaculos`)}
          >
            {planoUsuario ? "Assinatura" : "Meus espetáculos"}
          </S.ButtonContinuar>
        </S.AreaButtons>

        {planoUsuario?.name?.includes("Anual") &&
          <>
            <S.Spann size="1.2" className="pt-2" cor="#E4B810">
             Deseja comprar mais óculos VR? 
            </S.Spann>

            <S.PopapButton style={{border: '1px solid gold', borderRadius:'6px', background:'white'}} onClick={() => {setEhVr(true); document.getElementById('root').scrollTo(0, { behavior: "smooth" })}}>
              <S.Spann style={{color:'black', fontWeight: 'bold'}} size="1.5">Comprar óculos VR</S.Spann>
            </S.PopapButton>
          </>
        }
      </S.Container>
      <Footer />
      </>}
      

      {ehVr && (
        <Layout>
        <S.ContainerModal>
          <S.PopapDiv style={{ height: "100%" }}>
            <S.PopapDiv1 style={{ height: "100%", padding: "20px" }}>

              <S.Spann style={{fontWeight: 'bold'}} cor="black" size="1.8" className="mb-2 mt-2">
                Comprar óculos VR
              </S.Spann>

              <S.Spann cor="black" size="1.8" className="mt-2 mb-5">
                Acesse o link do nosso parceiro e compre o seu. <br/><br/>Uso exclusivo para as peças em visualização VR do PalcoWeb.  <br/><br/>Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação. Não nos responsabilizamos pelo uso inadequado do produto.<br/>
                
                <form style={{textAlign:'center', justifyContent:'space-evenly'}} onClick={() => setChecked(!checked)}>
                  <input style={{width:'30%'}} type="checkbox" />
                  <label>Li e aceito.</label>
                </form>

                {!checked && <div id='glow'>Aceite os termos antes de prosseguir.</div>}
              </S.Spann>

              <S.PopapDivButton>
                <S.PopapButton style={{background:'transparent'}} onClick={() => setEhVr(false)}>
                  <S.Spann style={{color:'black'}} size="1.5">Voltar</S.Spann>
                </S.PopapButton>

                <S.PopapButton onClick={() =>  checked ? window.open('https://produto.mercadolivre.com.br/MLB-1814036328-oculos-realidade-virtual-3d-compativel-com-todos-smartphones-_JM') : handleStyle()}>
                  <S.Spann size="1.5">Comprar</S.Spann>
                </S.PopapButton>
              </S.PopapDivButton>

            </S.PopapDiv1>
          </S.PopapDiv>
        </S.ContainerModal>
      </Layout>
      )}
      
    </>
  );
}

export default ConfirmCompra;
