import React, { useState, useContext } from "react";
// import { Link } from "react-router-dom";

import * as S from "./styles";
// import logo from "../../assets/logoPalcoweb.png";
import house from "../../assets/house.png";
// import User from "../../Connections/user";
// import Button from "../Button";
import { useHistory } from "react-router-dom";
// import app from "../../auth/config";
import firebase from "firebase";
import { AuthContext } from "../../auth/AuthContext";
import { useAuth } from "../../auth/auth";

function Header({ page, setPoupUp }) {
  const {userSigned, userIsAuthenticated, signOut, signature} = useAuth();
  const [collapse, setCollapse] = useState(false);
  
  const history = useHistory();
  
  const { currentUser } = useContext(AuthContext);

  window.addEventListener("click", function(event) {
    if (collapse === true) {
      setTimeout(()=>setCollapse(false), "5000")
    }
  });

  if (userIsAuthenticated()) {
    return (
      <S.Container>
        <img src={house} alt="Logotipo Palcoweb" onClick={() => history.push("/")} />
        
        <button style={{background:"white", color:"black", borderRadius:"6px", border:"transparent", marginLeft:"2vw", padding: "8px 10px", fontWeight:"bold", fontSize: "small"}}
                onClick={() => history.push(`/categorias`)}
          >Espetáculos</button>
        

        <button style={{background:"white", color:"black", borderRadius:"6px", border:"transparent", marginLeft:"2vw", padding: "8px 10px", fontWeight:"bold", fontSize: "small", whiteSpace: "nowrap"}}
                onClick={() => history.push(`/saibaMais`)}
          >Saiba mais</button>
        
        <S.CartContainer>
          <div style={{display:'flex', flexDirection:'column', justifyContent:'space-around', textAlign:'center', alignItems:'center'}}> 
              <S.NomeLogado >{userSigned && userSigned.fullName}</S.NomeLogado>
              {userSigned && firebase.auth().currentUser?.photoURL == null ? <i style={{fontSize:"30px"}} className="bi bi-person-fill" onClick={() => {collapse === false ? setCollapse(true): setCollapse(false)}}></i> : <img onClick={() => {collapse === false ? setCollapse(true): setCollapse(false)}}  style={{width:'50px',height:'50px',borderRadius:"15px"}} src={firebase.auth().currentUser?.photoURL}></img>}
          </div>
          
          <i className="bi bi-cart3" style={{fontSize:"30px"}} onClick={() => history.push("/cart")}></i>
        </S.CartContainer>
        {collapse ?
          <S.Collapse>
            <S.ModalMenu>
              <div
                onClick={() => history.push('/espetaculos/assinante') }
                >Assinatura</div>
              <div
                onClick={() => userSigned && userSigned.profileId==='2ad42cbc-b42f-49dc-92db-a61941e67706' ? history.push('/espetaculos'):history.push('/espetaculos/mkt') }
                >Meus Espetáculos</div>
              <div
                onClick={() => history.push('/perfil') }
                >Minha Conta</div>
              <div
                onClick={() => {signOut(); history.push("/")}}
                >Sair</div>
            </S.ModalMenu>
            
          </S.Collapse> : <div></div>
        }
      </S.Container>
    )
  } else {
    return (
      <S.Container>
        <img src={house} alt="Logotipo Palcoweb" onClick={() => history.push("/")} />

        <button style={{background:"white", color:"black", borderRadius:"6px", border:"transparent", marginLeft:"2vw", padding: "8px 10px", fontWeight:"bold", fontSize: "small"}}
                onClick={() => history.push(`/categorias`)}
          >Espetáculos</button>
        

        <button style={{background:"white", color:"black", borderRadius:"6px", border:"transparent", marginLeft:"2vw", padding: "8px 10px", fontWeight:"bold", fontSize: "small", whiteSpace: "nowrap"}}
                onClick={() => history.push(`/saibaMais`)}
          >Saiba mais</button>
        
        <S.CartContainer >
        <button style={{background:"#FDCC12", color:"black", borderRadius:"6px", border:"transparent", marginLeft:"2vw", padding: "8px 10px", fontWeight:"bold", fontSize: "large"}}
                onClick={() => history.push(`/login`)}
          >Entrar</button>

          <i className="bi bi-cart3" style={{fontSize:"30px"}} onClick={() => history.push("/cart")}></i>
        </S.CartContainer>
      </S.Container>
    );
  }
}

export default Header;