import React, { useEffect, useState } from "react";
import { SwiperSlide } from "swiper/react";
import { useHistory } from "react-router";
import { FaPlay } from "react-icons/fa";
import * as S from "./styles";

import Layout from "../../layout";

import Share from "../../components/CardCompartilhar/index";
import instagram from "../../assets/instagram-yellow.png";
import facebook from "../../assets/facebook-yellow.png";
import ImgIcon from "../../assets/icon.png";
import Section from "../../components/Section";
import Card from "../../components/Card";
import HorizontalList from "../../components/HorizontalList";
import CanalPwThumb from "../../assets/banner-live.png";
import BannerThumb1 from "../../assets/gheu.png";
import BannerThumb2 from "../../assets/tvpwMargarethCard.png";
import BannerThumb3 from "../../assets/card-cris.png";
import BannerThumb4 from "../../assets/card-ribamar.png";
import BannerThumb5 from "../../assets/card-eduardo.png";
import BannerThumb6 from "../../assets/card-bia.png";

function CanalPw() {
  const history = useHistory();
  const [compartilharFica, setcompartilharFicha] = useState(false);
  const [popup, setPopup] = useState(false);
  const listfak = ["1", "2", "3", "4", "5", "6"];
  const BannerThumb = [
    BannerThumb1,
    BannerThumb2,
    BannerThumb3,
    BannerThumb4,
    BannerThumb5,
    BannerThumb6,
  ];
  const Synopsis = [
    "Gestora de Marketing Cultural, Gheu Tibério abordando os desafios do teatro online, e tudo que rodeia esse taboo no meio artístico.",
    'Produtora Cultural, Margareth Heineck abordando o teatro com a "nova" Lei Rouanet.',
    "Autora do livro Ana e a Tal da Felicidade, Cris Pimentel abordando sobre como produzir uma peça.",
    "Ribamar Ribeiro, ator, diretor, produtor, professor e sonoplasta, abordando o teatro como profissão.",
    "Eduardo Almeida, ator, produtor, professor e fotógrafo, abordando sobre o teatro para a família.",
    "Bia Junqueira, diretora de arte e cenógrafa renomada (Arquitetura de Efêmero), abordando sobre a importância da cenografia e ser futuro.",
  ];
  const TvpwLink = [
    "/playcanalpw/685622848/25026db3ca",
    "/playcanalpw/688208901/a305fa6b6e",
    "/playcanalpw/690110285/59a5ecef8b",
    "/playcanalpw/690717734/a56337b313",
    "/playcanalpw/696782348/702f7d0658",
    "/playcanalpw/699612824/445f65c5df",
    "/tvpwaovivo/1919099",
  ];
  const [canais] = useState({});
  useEffect(() => {
    setTimeout(function () {
      window.scrollTo(0, 0);
    }, 2);
  }, []);
  return (
    <>
      {/** Popap 1 */}
      {!popup && (
        <Layout>
          <S.Container>
            <S.PopapDiv style={{ height: "100%" }}>
              <S.PopapDiv1 style={{ height: "100%", padding: "50px" }}>
                <S.Span cor="black" size="1.8" className="mb-2 mt-2">
                  O que é a TV PalcoWeb ?
                </S.Span>
                <S.Span cor="black" size="1.8" className="mt-2 mb-5">
                  Programa de entrevistas e palestras com diretores, produtores
                  e artistas da área de teatro e cultural.
                </S.Span>
                <S.PopapDivButton>
                  <S.PopapButton onClick={() => setPopup(!popup)}>
                    <S.Span size="1.5">Acessar</S.Span>
                  </S.PopapButton>
                </S.PopapDivButton>
              </S.PopapDiv1>
            </S.PopapDiv>
          </S.Container>
        </Layout>
      )}

      {popup && (
        <Layout>
          <S.Container>
            <S.ContainerImg>
              <S.Thumbnail src={CanalPwThumb}>
                <S.BtnAssitir
                  className="btn"
                  onClick={() =>
                    history.push("/playcanalpw/696782348/702f7d0658")
                  }
                >
                  <FaPlay />
                </S.BtnAssitir>
              </S.Thumbnail>
            </S.ContainerImg>

            {/** Popap 2 */}

            <S.Conteudo>
              {/* <S.ButtonVoltar
                onClick={() => history.push("/canais")}
              >
                {" "}
                <i className="bi bi-x-lg"></i>{" "}
              </S.ButtonVoltar> */}

              <S.HeaderConteudo>
                <S.Item>
                  <S.Coracao>
                    <i className="bi bi-suit-heart"></i>
                  </S.Coracao>

                  <S.Compartilhar>
                    {" "}
                    <i
                      className="bi bi-share-fill"
                      onClick={() => setcompartilharFicha(true)}
                      style={{ cursor: "pointer" }}
                    ></i>
                    {compartilharFica && (
                      <Share
                        link={`http://www.palcoweb.com.br/#/canalpw`}
                        sairficha={setcompartilharFicha}
                      ></Share>
                    )}
                  </S.Compartilhar>
                </S.Item>
                <S.Img
                  src={ImgIcon}
                  style={{
                    margin: 0,
                    width: 100,
                    height: 100,
                    borderRadius: "50%",
                  }}
                  value={13}
                  alt="imagem referente ao canal"
                />
                <S.Item>
                  <S.HeaderTitle>
                    {" "}
                    <S.Span size="1">TV PalcoWeb</S.Span>
                  </S.HeaderTitle>
                  <div>
                    {/*redes sociais */}
                    {canais?.facebook && (
                      <a
                        href={canais.facebook}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        {" "}
                        <img src={facebook} alt="logo do facebook" />
                      </a>
                    )}
                    {canais?.instagram && (
                      <a
                        href={"https://www.instagram.com/" + canais?.instagram}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        {" "}
                        <img src={instagram} alt="logo do instagram" />
                      </a>
                    )}
                  </div>
                  {canais.site && (
                    <S.Span
                      cor="#fdcc12"
                      size="1"
                      style={{ cursor: "pointer" }}
                    >
                      {" "}
                      <a
                        href={canais.site}
                        target="_blank"
                        rel="noopener noreferrer"
                        style={{ color: "#fdcc12", textDecorationLine: "none" }}
                      >
                        Site Oficial
                      </a>{" "}
                    </S.Span>
                  )}
                </S.Item>
              </S.HeaderConteudo>

              <S.Informacoes>
                <S.Item>
                  <S.Text size="0.8">
                    Tv palco web é um programa de variedades exibido na TV Palco
                    web. O programa apresentado pelo CEO JORGE ALBERTO ZIETLOW
                    DURO Graduado em Administração Pública e de Empresas pela
                    UFRGS , Mestre em Administração pela PUC-Rio e Doutor pela
                    PUC-Rio. Diretor da DataCorp Mercadata do Brasil Ltda.
                    Consultor há mais de vinte anos e entusiasta nato pelas
                    artes e culturas de todo mundo. Traçando um Roteiro
                    intrigrante desvendando os mitos e misterios em suas
                    entrevista e palestras com diretores, produtores e artistas
                    da área de teatro e cultural.
                  </S.Text>
                </S.Item>
                <S.Item>
                  {/* <S.BtnAssitir className="btn btn-warning" onClick={()=> history.push(`/tvpwaovivo/${'1919099'}`)}>
                   
                      Assistir
                    
                  </S.BtnAssitir> */}
                </S.Item>
              </S.Informacoes>
            </S.Conteudo>
          </S.Container>

          <Section title="Entrevistas" filter>
            <HorizontalList id="emAlta">
              {listfak?.map((card, index) => {
                return (
                  <SwiperSlide key={String(index)}>
                    {card === "" ? (
                      <Card noContent />
                    ) : (
                      <S.CardTest>
                        <S.Img
                          src={BannerThumb[index]}
                          style={{
                            position: "absolute",
                            width: 250,
                            height: 400,
                          }}
                          value={card.key}
                        />

                        {
                          <S.InfoCard>
                            {/** 
                            <S.InfoCardConteudo>
                           
                              <S.ClassificacaoCard>
                                {card.classificacao}
                              </S.ClassificacaoCard>
                              <S.Coracao>
                                <i className="bi bi-suit-heart-fill"></i>
                              </S.Coracao>
                              <S.Compartilhar>
                                <i
                                  className="bi bi-share-fill"
                                  onMouseEnter={() => setcompartilhar(true)}
                                ></i>
                                {compartilhar && (
                                  <Share sair={setcompartilhar}></Share>
                                )}
                              </S.Compartilhar>
                            </S.InfoCardConteudo>
                              */}
                            <S.Titulo>{card.title}</S.Titulo>
                            <S.ConteudoCard
                              style={{ textTransform: "uppercase" }}
                            >
                              {card.categoria}
                            </S.ConteudoCard>
                            <S.Sinopse>{Synopsis[index]}</S.Sinopse>

                            <S.ButtonAcessar
                              className="btn btn-warning"
                              onClick={() => history.push(TvpwLink[index])}
                            >
                              Assistir{" "}
                            </S.ButtonAcessar>
                          </S.InfoCard>
                        }
                      </S.CardTest>
                    )}
                  </SwiperSlide>
                );
              })}
            </HorizontalList>
          </Section>
        </Layout>
      )}
    </>
  );
}
export default CanalPw;
