import React, { useEffect, useState } from "react";
import { Redirect, Route, useHistory } from "react-router-dom";
import { useAuth } from "../auth/auth";
import { refreshToken } from "../Connections/apis";
import profiles from "../utils/models/profiles";


const PrivateRoutes = ({ role,isSignature, ...rest }) => {
  const {userSigned, signature} = useAuth();
  const history = useHistory();

  const [permissions, setPermissions] = useState([]);

  useEffect(() => {
    async function loadRoles() {
      const findRole = role?.split(",").includes(profiles[userSigned.profileId].toUpperCase())
      setPermissions(findRole);
    }

    loadRoles();
  }, [userSigned, role]);

  useEffect(()=>{
    refreshToken()
  },[])


  const { userIsAuthenticated } = useAuth();
  if (!userIsAuthenticated()) {
    history.push('/login',{redirect:true})
  }

  if (!role && userIsAuthenticated()) {
    return (
      <Route {...rest} />
    );
  }
  else if(permissions && !isSignature){
    return (
      <Route {...rest} />
    );
  }
  else if(permissions && isSignature && userIsAuthenticated()){
    return signature.isActive ? <Route {...rest} /> : <Redirect to="/Assinatura" />
  }
  else{
    history.push('/login',{redirect:true})
  }
};

export default PrivateRoutes;