import styled from 'styled-components';

export const TitleDuvidas = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
  margin: 0;
  padding: 0;

  @media(min-width: 900px) {
    font-size:30px;
  }
`;

export const BtnNoStyle = styled.button`
  background:transparent; 
  color:#fff; 
  text-decoration: underline; 
  font-Size: large; 
  cursor: pointer; 
  border:none;

  @media (min-width: 900px) {
      font-Size: x-large; 
  }
`;
