import React, { useState } from "react";

import * as S from "./styles";
import logoPW from "../../assets/logo-pw.png";

import { useHistory } from "react-router-dom";
import "./style.css";
import Layout from "../../layout";
import { currency } from "../../utils/utils";
import { useCarts } from "../../dados/context/CartContext";
import Popup from "../../components/PopUpCupom";
import { usePayments } from "../../dados/context/PaymentContext";

function Cart() {
  const { cartList, removeToCart } = useCarts();
  const [abrirPopup, setabrirPopup] = useState();
  const { addToPayment } = usePayments();
  const [cupomInput, setCupomInput] = useState("");
  const [closePopup, setClosePopup] = useState(false);
  const [returnValue, setReturnValue] = useState(false);
  const [showModal3d, setShowModal3d] = useState(false);
  const [ehVr, setEhVr] = useState(false);
  const [checked, setChecked] = useState(false);


  const history = useHistory();

  function verifyVoucherPeça(voucher) {
    setabrirPopup(!abrirPopup);
  }

  function payment() {
    if (Object.keys(cartList).find((item) => item === "signature_id")) {
      addToPayment(cartList);
      history.push("/pagamento");
    } else {
      addToPayment(cartList);
      history.push("/pagamento");
    }
  }

  // console.log(cartList.num_vr)

  function handleStyle () {
    const glow = document.getElementById('glow')
    glow.style.color = 'red' 
    glow.style.fontWeight = 'bold'
  }

  return (
    <Layout>
      {showModal3d &&
    
      <S.PopapDiv>
        <S.PopapDiv1 >

          <span style={{cursor: "pointer", float: "right", marginRight: "-90%"}} onClick={() => setShowModal3d(false)} size="1.5">X</span>

          <S.Span style={{fontWeight: 'bold'}} cor="black" size="2">
            Promoção 3D 
          </S.Span>

          <S.Span cor="black" size="1.8" >
          
            Na compra da assinatura anual você ganha um óculos VR.
            
          </S.Span>

          <S.Span cor="black" size="1.8" >
          
            Após a compra você receberá o óculos VR para seu uso em regime de comodato ficando sob sua responsabilidade. Uso exclusivo para as peças em visualização VR do PalcoWeb. Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação.
            
            <form style={{textAlign:'center', justifyContent:'space-evenly'}} onClick={() => setChecked(!checked)}>
              <input style={{width:'30%'}} type="checkbox" />
              <label>Li e aceito.</label>
            </form>

            {!checked && <div id='glow'>Aceite os termos antes de prosseguir.</div>}
          </S.Span>
       
          <S.PopapDivButton>
            <S.PopapButton onClick={(e) => checked ? setShowModal3d(false) : handleStyle() }>
              <S.Span size="1.5">Continuar</S.Span>
            </S.PopapButton>
          </S.PopapDivButton>

          <b>Não nos responsabilizamos pelo uso inadequado do produto.</b>

        </S.PopapDiv1>
      </S.PopapDiv>
      }

      {ehVr && (
        <S.PopapDiv>
          <S.PopapDiv1>

            <S.Span style={{fontWeight: 'bold'}} cor="black" size="1.8" className="mb-2 mt-2">
              Comprar óculos VR
            </S.Span>

            <S.Span cor="black" size="1.8" className="mt-2 mb-5">
              Acesse o link do nosso parceiro e compre o seu. <br/><br/>Uso exclusivo para as peças em visualização VR do PalcoWeb.  <br/><br/>Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação. Não nos responsabilizamos pelo uso inadequado do produto.<br/>
              
              <form style={{textAlign:'center', justifyContent:'space-evenly'}} onClick={() => setChecked(!checked)}>
                <input style={{width:'30%'}} type="checkbox" />
                <label>Li e aceito.</label>
              </form>

              {!checked && <div id='glow'>Aceite os termos antes de prosseguir.</div>}
            </S.Span>

            <S.PopapDivButton>
              <S.PopapButton style={{background:'transparent'}} onClick={() => setEhVr(false)}>
                <S.Span style={{color:'black'}} size="1.5">Voltar</S.Span>
              </S.PopapButton>

              <S.PopapButton onClick={() =>  checked ? window.open('https://produto.mercadolivre.com.br/MLB-1814036328-oculos-realidade-virtual-3d-compativel-com-todos-smartphones-_JM') : handleStyle()}>
                <S.Span size="1.5">Comprar</S.Span>
              </S.PopapButton>
            </S.PopapDivButton>

          </S.PopapDiv1>
        </S.PopapDiv>
      )}

      {!showModal3d && !ehVr && <S.Wrapper>
        <S.Container noItem={!cartList.id}>
          {!cartList.id ? (
            <S.CardFilmeTitle>
              <S.Cart>
                <i className="bi bi-cart-x-fill"></i>
              </S.Cart>
              <span>
                Encontre um de Nossos Espetáculos e adicione ao carrinho :D
              </span>
            </S.CardFilmeTitle>
          ) : (
            <>
              <S.Header>
                <span>Carrinho de compras</span>
              </S.Header>
              {abrirPopup === true ? (
                <Popup
                  cupom={cupomInput}
                  tipo={"espetaculo"}
                  closePopup={setabrirPopup}
                  plano={"Anual"}
                  returnValue={setReturnValue}
                />
              ) : (
                <></>
              )}
              <S.CartContainer>
                <S.CartLinha>
                  <S.CartFilme>
                    <S.X onClick={() => removeToCart()}>
                      <i className="bi bi-x-lg"></i>
                    </S.X>
                    
                    {cartList.thumbnail ? (
                      <S.Img src={cartList.thumbnail}></S.Img>
                    ) : (
                      <S.ImgPlanos src={logoPW}></S.ImgPlanos>
                    )}
                    <S.CartFilmeInfo>
                      <S.CardFilmeTitle>
                        {cartList.tituloEsp || "Plano " + cartList.name}
                      </S.CardFilmeTitle>
                      <S.CardFilmeSubTitle>
                        {cartList?.name?.includes("Anual") && "Promoção Óculos 3D" }
                      </S.CardFilmeSubTitle>
                      <S.CardFilmeSubTitle>
                        {cartList?.name?.includes("Anual") &&  <S.ButtonPagar onClick={() =>setShowModal3d(true)}>Mais Detalhes</S.ButtonPagar>}
                      </S.CardFilmeSubTitle>
                      {/* <S.CardFilmeSubTitle>
                        {cartList?.name?.includes("Anual") && "+ " + cartList.num_vr + " óculos vr inclusos" }
                      </S.CardFilmeSubTitle> */}
                      {/* "Na compra da assinatura anual você ganha um óculos VR." */}
                    </S.CartFilmeInfo>
                  </S.CartFilme>
                  <S.CardFilmeValue>
                    {cartList.valorPreco
                      ? currency(cartList.valorPreco)
                      : currency(cartList.value)}
                  </S.CardFilmeValue>
                </S.CartLinha>
              </S.CartContainer>
              <div style={{ display: "block", right: "0", margin: "0" }}>
                <S.InputCumpom
                  // style={{display:'flex', justifyContent:''}}
                  placeholder="PREENCHA SEU CUPOM"
                  value={cupomInput}
                  onChange={(e) => {
                    setCupomInput(e.target.value);
                  }}
                  className="inputCumpom"
                />
              </div>

              <S.CartActions>
                <S.ButtonVoltar
                  onClick={() =>   cartList?.name?.includes("Anual" || "Mensal" || "Trimestral") ? history.push('/') : history.push(`/Cardezao/${cartList.id}`)}
                >
                  <i className="bi bi-chevron-left"></i>Voltar{" "}
                </S.ButtonVoltar>

                {/* <S.ButtonPagar
                  onClick={() => {
                    addToPayment({
                      ...cartList,
                      spectacle_id: cartList.id,
                      spectacle_value: cartList.valorPreco,
                    });
                    history.push(`/pagamento`);
                  }}
                >
                  Pagar
                </S.ButtonPagar> */}

                {cupomInput && returnValue === false ? (
                  <div
                    className="valid "
                    onClick={() => verifyVoucherPeça(cupomInput)}
                  >
                    <S.ButtonPagar>Verificar</S.ButtonPagar>
                  </div>
                ) : (
                  <>
                    <S.ButtonPagar onClick={() => payment()}>
                      Pagar
                    </S.ButtonPagar>
                  </>
                )}
              </S.CartActions>

              {cartList?.name?.includes("Anual" || "Mensal" || "Trimestral") &&
                <S.CartActions style={{flexDirection:'column', textAlign:'center', alignItems:'center', justifyContent:'center'}}>
                  <S.Span size="1.2" className="pt-2" cor="#E4B810">
                  Deseja comprar mais óculos VR? 
                  </S.Span>

                  <S.PopapButton style={{border: '1px solid gold', borderRadius:'6px', background:'white', justifyContent:'center'}} onClick={() => {setEhVr(true); document.getElementById('root').scrollTo(0, { behavior: "smooth" })}}>
                    <S.Span style={{color:'black', fontWeight: 'bold'}} size="1.5">Comprar óculos VR</S.Span>
                  </S.PopapButton>
                </S.CartActions>
              }
            </>
          )}
        </S.Container>
      </S.Wrapper>
      }
    </Layout>
  );
}

export default Cart;
