import styled from 'styled-components';

export const SectionContainer = styled.div`
  position: relative;
  width: 100%;
  height: 270px;
  margin: 30px 5px;

  @media (min-width: 900px){
    height: 450px;
  }
`;

export const ListRow = styled.div`
  height: 270px;
  width: auto;
  overflow: hidden;
  white-space: nowrap;
  display: inline-block;
  text-align: center;
  padding-bottom: 10px !important;
  display: flex;
  align-items: center;

  @media (min-width: 900px){
    height: 450px;
  }
`;

export const ListItem = styled.div`

  min-width: 150px;
  max-width: 150px;
  height: 250px;
  background-image: url(${(props) => props.src});
  background-size: cover;
  background-position: center;
  background-color: gray;
  cursor: pointer;
  transition: 0.5s ease;
  z-index: 2;

  transform: scale(0.9);

  &:hover {
    transform: scale(1);
    z-index: 5;
  }

  @media (min-width: 900px){
    min-width: 250px;
    max-width: 250px;
    height: 450px;
  }

`;

export const NavPrev = styled.div`
    position: absolute;
    width: 40px;
    height: 260px;
    z-index: 9;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 0;
    overflow: hidden;
    background-color: rgba(0, 0, 0, 0.6);
    cursor: pointer;

    @media (min-width: 900px){
      height: 450px;
    }
`;

export const NavNext = styled(NavPrev)`
    right: 0;
    left: auto;
`;

export const InfoCard = styled.div`
  min-width: 150px;
  max-width: 150px;
  height: 250px;

  @media (min-width: 900px){
    min-width: 250px;
    max-width: 250px;
    height: 450px;
  }

  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 15px;
  z-index: 8;
  transform: scale(0.9);
 
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  
  :hover {
    opacity: 1;
    transform: scale(1);
  }

`;

export const ButtonAcessar = styled.button`

  margin: auto;
  color: black;
  font-size: 12px;
  width: 60%;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 15px;

  :hover {
    background-color: white;
    color: black;
    border-color: white;
  }
`;


