import axios from 'axios';
import { psUrl } from './consts';

const apiPs = axios.create({
    baseURL: psUrl,
});

apiPs.interceptors.request.use(config => {
    if (process.env.REACT_APP_PAYMENT_TOKEN_PS) {
        config.headers.Authorization = `Bearer ${process.env.REACT_APP_PAYMENT_TOKEN_PS}`;
        config.headers["Access-Control-Allow-Origin"] = process.env.REACT_APP_URL_APP_PS;
        config.headers["Content-Type"] = "application/json";
    }

    return config;
});



export default apiPs;