import React, { useEffect, useState, createContext, useContext } from "react";

const Carts = createContext({});

export function CartsProvider(props) {
  const [cartList, setCart] = useState({});
  const [listaPlanos, setListaPlanos] = useState({ teste: "ok" });
  const [pagou, setPagou] = useState(null);

  useEffect(() => {
    getStorage();
  }, []);

  async function AdicionarPlanos(objeto) {
    if (objeto) {
      await setListaPlanos(objeto);
      console.log("ok");
    } else {
      console.log("not ok");
    }
  }

  function getStorage() {
    if (localStorage.getItem("Cart:item")) {
      setCart(JSON.parse(localStorage.getItem("Cart:item")));
    }
  }

  function addToCart(item) {
    setCart(item);
    localStorage.setItem("Cart:item", JSON.stringify(item));
  }

  function removeToCart() {
    setCart({});
    localStorage.removeItem("Cart:item");
  }

  return (
    <Carts.Provider
      value={{
        addToCart,
        removeToCart,
        cartList,
        pagou,
        setPagou,
        AdicionarPlanos,
        listaPlanos,
      }}
    >
      {props.children}
    </Carts.Provider>
  );
}

export const useCarts = () => useContext(Carts);
