import React from "react";
import { BrowserRouter } from "react-router-dom";
import { ThemeProvider } from "styled-components";
import { AuthProviderMain } from "../auth/auth";
import { AuthProvider } from "../auth/AuthContext";
import { CartsProvider } from "../dados/context/CartContext";
import { PaymentsProvider } from "../dados/context/PaymentContext";
import { PlansProvider } from "../dados/context/PlansContext";
import { ShowsProvider } from "../dados/context/ShowsContext";
import Routes from "../routes";
import theme from "../theme";
import { GlobalStyle } from "../theme/global";

import 'react-toastify/dist/ReactToastify.css';

function App() {
  return (
    <BrowserRouter>
      <GlobalStyle />
      <ThemeProvider theme={theme}>
        <ShowsProvider>
          <PlansProvider>
            <CartsProvider>
              <PaymentsProvider>
                <AuthProviderMain>
                  <AuthProvider>
                    <Routes />
                  </AuthProvider>
                </AuthProviderMain>
              </PaymentsProvider>
            </CartsProvider>
          </PlansProvider>
        </ShowsProvider>
      </ThemeProvider>
    </BrowserRouter>
  );
}

export default App;
