import React, { useRef, useState, useEffect } from "react";
import * as S from "./styles.js"
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import { useHistory } from "react-router-dom";

import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import { useShows } from "../../dados/context/ShowsContext";
import Auth from "../popups/Auth/index.js";

export default function ChannelsCarousel({children, spectacles}) {
  
    const {
      getAllChannels,
      channelLike,
      isInFavoritesChannel,
      getAllChannelsFavoriteByUser
    } = useShows();
    const { userIsAuthenticated } = useAuth();
    const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";
    const history = useHistory();
  
    useEffect(() => {
      getAllChannels();
    }, []);
  
    useEffect(() => {
      userId && getAllChannelsFavoriteByUser(userId, false);
    }, []);

    var scrollPerClick = 300;
    var scrollAmount = 0;
  
    const slider = useRef(null)
  
    function handleNext() {
  
     if (scrollAmount <= (slider.current.scrollWidth - slider.current.clientWidth)) {
  
      slider.current.scrollTo({
        top: 0,
        left: (scrollAmount += scrollPerClick),
        behavior: "smooth"
      })
     }
  
    }
  
    function handlePrevious() {
  
      slider.current.scrollTo({
        top: 0,
        left: (scrollAmount -= scrollPerClick),
        behavior: "smooth"
      })
  
      if (scrollAmount < 0) {
        scrollAmount = 0;
      }
  
    }

  return (
    <>
      <S.SectionContainer>

        <S.NavPrev onClick={() => handlePrevious()}>
          <NavigateBeforeIcon
            style={{fontSize: 50}}
          ></NavigateBeforeIcon>
        </S.NavPrev>

        <S.NavNext onClick={() => handleNext()}>
          <NavigateNextIcon
            style={{fontSize: 50}}
          ></NavigateNextIcon>
        </S.NavNext>

        <S.ListRow
          ref={slider}
        >
          {spectacles.length > 0 &&
          spectacles.map((item, key) => {

            return (
              <S.ListItem src={item?.thumbnail} key={key}>
                <S.InfoCard>
                    <S.Span
                        size="14"
                        style={{ marginBottom: "15px" }}
                    >
                        {userIsAuthenticated() && (
                            <S.Coracao
                              channelIsInFavorites={isInFavoritesChannel(item?.id)}
                              onClick={() => {
                                channelLike(userId, item?.id)
                                
                              }}
                            >
                              <i className="bi bi-suit-heart"></i>
                            </S.Coracao>
                          )}
                        </S.Span>
                        <S.Span size="14">
                          {item?.fullName}
                        </S.Span>

                        <S.Span size="10">
                          <i className="bi bi-circle-fill"></i>{" "}
                          <S.Span size="15">
                            {item?.categoria}
                          </S.Span>{" "}
                        </S.Span>
                        <S.Span></S.Span>
                        <S.verMais
                          size="14"
                          style={{ marginTop: "15px" }}
                          onClick={() => {
                            history.push(`FichaCanal/${item?.id}`);
                            // console.log(item)
                            // window.location.reload();
                          }}
                        >
                          Ver mais
                        </S.verMais>

                </S.InfoCard>
              </S.ListItem>
            )
          })}
        </S.ListRow>
      </S.SectionContainer>
    </>
  );
}