import styled from "styled-components";

export const Hr = styled.div`
  position: relative;
  right: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  height: 1px;
  transition: 0.3s;
`;

export const Container = styled.div`
  &:hover .line {
    width: 60%;
    background-color: #fdcc12;
  }
  padding: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 250px;
  color: #fff;
  height: auto;
  background-color: #000;
  flex-wrap: wrap;
  border-radius: 15px;
`;

export const SignatureDuration = styled.div`
  p {
    font-style: Ubuntu;
    font-weight: 100;
    font-size: 2em;
    line-height: 60px;
  }
  display: flex;
  flex-wrap: no-wrap;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 60px;
`;

export const SignaturePrice = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  h2 {
    font-size: 1.5em;
    text-decoration: line-through;
  }
  padding-top: 15px;
  letter-spacing: -2px;
`;

export const AssignButton = styled.span`
  border-radius: 12px;
  width: 80%;
  justify-self: flex-end;
  outline: 0;
  border: 0;
  background-color: #fdcc12;
`;
