import React, {
  createContext,
  useCallback,
  useContext,
  useEffect, 
  useState
} from "react";
import app from "./config"
import api from "../Connections/apis";

const AuthContext = createContext({});

const AuthProviderMain = ({ children }) => {
  const [token, setToken] = useState(() => {
    const token = localStorage.getItem("@PermissionYT:token");

    if (token) {
      return token;
    }

    return null;
  });
  const [user, setUser] = useState(null);
  const [userSigned, setUserSigned] = useState(() => {
    const user = localStorage.getItem("@PermissionYT:user");

    if (user) {
      return JSON.parse(user);;
    }

    return null;
  });
  const [signature, setSignature] = useState(() => {
    const signature = localStorage.getItem("@PermissionYT:signature");

    if (signature) {
      return JSON.parse(signature);;
    }

    return null;
  });
  const [signatureReport, setSignatureReport] = useState(null);
  const [refreshToken] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  useEffect(() => {
    if (user) {
      getTokenFirebase()
    }
  }, [user]);


  useEffect(() => {
    const user = localStorage.getItem("@PermissionYT:user");
    setUserSigned(JSON.parse(user));
  }, [])

  useEffect(() => {
    const signature = localStorage.getItem("@PermissionYT:signature");
    setSignature(JSON.parse(signature));
  }, [])

  useEffect(() => {
    const signatureReport = localStorage.getItem("@PermissionYT:signatureReport");
    setSignatureReport(JSON.parse(signatureReport));
  }, [])

  const userIsAuthenticated = useCallback(() => {
    const tk = localStorage.getItem("@PermissionYT:token");

    if (tk) {
      return true
    } else {
      return false
    }
  }, [token]);

  const getTokenFirebase = async() => {
    if (user) {
      const userId = app.auth().currentUser.uid;
      try {
        const res = await app.firestore().collection('users').doc(userId).get()
        const userIsclient = res && res.data()?.profileId==='2ad42cbc-b42f-49dc-92db-a61941e67706' || res.data()?.profileId==='742ffa33-acb5-4ecd-9307-4a4459d44024';
        if(userIsclient){
          // setUserSigned(res.data());
          // localStorage.setItem("@PermissionYT:user", JSON.stringify(res.data()));
          setUserStorage(res.data())
          app.auth().currentUser.getIdToken().then(res => {
            // console.log('firebaseToken', res)
            tokenApi(res)
            // setToken(res)

          })
            .catch(error => {
              if(error.response){
                setLoading(false)
                setError(true)
                console.log(error.response.message);
              }

            });
        }else{
          setLoading(false)
          setError(true)
        }

      } catch (err) {
        console.log(err);
        setLoading(false)
        setError(true)
      }
    }
  }

  // const setUserFirebaseToStorage = async () => {
  //   if (user) {
  //     const userId = app.auth().currentUser.uid;
  //     try {
  //       const res = await app.firestore().collection('users').doc(userId).get()
  //       setUserSigned(res.data());
  //       localStorage.setItem("@PermissionYT:user", JSON.stringify(res.data()));
  //     } catch (err) {
  //       console.log(err);
  //     }
  //   }
  // }


function addNewUserToFirestore(user) {
  
  const collection = app.firestore().collection('users');
  const {profile} = user.additionalUserInfo;
  const details = {
    fullName: profile.name,
    email: profile.email,
    picture: profile.picture,
    profileId:'2ad42cbc-b42f-49dc-92db-a61941e67706',
    createdDtm: Date.now(),
    lastLoginTime: Date.now(),
  };
  console.log(app.auth().currentUser.uid);
  collection.doc(app.auth().currentUser.uid).set(details);
  console.log("teste");
  return {user, details};
}


  const tokenApi = (res) => {
    if (user) {
      api.post('/auth', {}, {
        headers: {
          webstage_auth: process.env.REACT_APP_API_SECRET_CODE, 
          Authorization: `Bearer ${res}`
        }
      }).then(tokenapi => {
        const {accessToken, refreshToken, signature} = tokenapi.data.body;
        const {plan, ...rest} = signature
        setToken(accessToken);
        setSignature(rest);
        setSignatureReport(plan)
        localStorage.setItem("@PermissionYT:token", accessToken);
        localStorage.setItem("@PermissionYT:signature", JSON.stringify(rest));
        // setUserFirebaseToStorage();
        localStorage.setItem("@PermissionYT:refreshToken", refreshToken);
        localStorage.setItem("@PermissionYT:signatureReport", JSON.stringify(plan));
        localStorage.setItem("@PermissionYT:user", JSON.stringify(res.data()));

        setLoading(false);
        setError(false)
      }).catch(err => {
        if(err.response){
        setLoading(false)
        setError(true)   
        }

      })
    }

  }

  const setUserStorage = (user)=>{

    const hasUser = localStorage.getItem("@PermissionYT:user");
    if(hasUser){
      const userParse = JSON.parse(hasUser)
      const newUser = {...userParse, ...user}
      localStorage.setItem("@PermissionYT:user", JSON.stringify(newUser));
      setUserSigned(newUser);
      return;
    }else{
      localStorage.setItem("@PermissionYT:user", JSON.stringify(user));
      setUserSigned(user);
      return;
    }
  }

 


    const signOut = () => {
    const token = localStorage.getItem('@PermissionYT:token')
    if (token) {
      app.auth().signOut()
      localStorage.clear()
      window.location.reload();
    }
  }

  const updateSignature =(value)=>{
    const hasSignature = localStorage.getItem('@PermissionYT:signature')
    if(hasSignature){
      localStorage.removeItem('@PermissionYT:signature')
      localStorage.setItem('@PermissionYT:signature', JSON.stringify({isActive:value}))
    }else{
      localStorage.setItem('@PermissionYT:signature', JSON.stringify({isActive:value}))
    }
  }


  const setRouteToStack =(route)=>{
    const hasSignature = localStorage.getItem('stack:route')
    if(hasSignature){
      localStorage.removeItem('stack:route')
      localStorage.setItem('stack:route', route)
    }else{
      localStorage.setItem('stack:route', route)
    }
  }

  return (
    <AuthContext.Provider value={
      {
        user,
        setUser,
        loading,
        setLoading,
        error,
        userIsAuthenticated,
        signOut,
        updateSignature,
        addNewUserToFirestore,
        setRouteToStack,
        token,
        signature,
        signatureReport,
        refreshToken,
        userSigned,
        setError
      }
    }>
      {children}
    </AuthContext.Provider>
  );
};

function useAuth() {
  const context = useContext(AuthContext);
  return context;
}

export { AuthProviderMain, useAuth };
