import { CardElement, useElements, useStripe } from "@stripe/react-stripe-js";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import { toast, ToastContainer } from 'react-toastify';
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Footer from "../../components/Footer";
import GetAddressByCep from "../../components/GetAddressByCep";
import { useCarts } from "../../dados/context/CartContext";
import { usePayments } from "../../dados/context/PaymentContext";
import { optionsToast } from "../../dados/context/ShowsContext";
import Layout from "../../layout";
import {
  convertToNumber,
  currency
} from "../../utils/utils.js";
import Header from "../../components/Header";
import "./style.css";
import * as S from "./styles";


const CARD_OPTIONS = {
  iconStyle: "solid",
  hidePostalCode: true,
  style: {
    base: {
      color: "#FFF",
      fontSize: "16px",
      fontFamily: '"Open Sans", sans-serif',
      fontSmoothing: "antialiased",
      "::placeholder": {
        color: "#FFF",
      },
    },
    invalid: {
      color: "#e5424d",
      ":focus": {
        color: "#303238",
      },
    },
  },
};

function Pagamento(props) {
  const {
    paymentList,
    paySignature,
    paySpectacle,
    validate,
    removeToPayment,
    error,
  } = usePayments();
  const { updateSignature, setRouteToStack } = useAuth();
  const {removeToCart} = useCarts();
  const [cpf, setCpf] = useState("");
  const [errors, setErrors] = useState([]);
  const [address, setAddress] = useState("");
  const [cep, setCep] = useState("");
  const [number, setNumber] = useState("");
  const [complement, setComplement] = useState("");

  const history = useHistory();
  const stripe = useStripe();
  const elements = useElements();

  const isSignature = paymentList.signature_id ? true:false;

  const handleSubmitAddress = async (e) => {

    e.preventDefault();
    
    const userId = app.auth().currentUser.uid;

    const dataAddress = {
      address: address,
      cep: cep,
      number: number,
      complement: complement
    };


    //VERIFICAR SE O CÓDIGO ABAIXO ESTÁ CORRETO
    //ADICIONA O CAMPO user_address NO OBJ DO USUÁRIO

    // app
    // .firestore()
    // .collection("users")
    // .doc(userId)
    // .set(
    //   {
    //     user_address: dataAddress,
    //   },
    //   { merge: true }
    // );

  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { nome } = e.target.elements;
    if (isDisabled()) {
      toast.warning("verifique os campos inválidos", optionsToast);
      return;
    }
    const { error: errorCard, paymentMethod } = await stripe.createPaymentMethod({
      type: "card",
      card: elements.getElement(CardElement),
    });
    setErrors(errorCard)
    // if(isDisabled() || errorCard){
    //   toast.error(`Ooops, algo deu errado ${errorCard && errorCard?.message}`, optionsToast);  
    //   return;
    // }
    const { id } = paymentMethod;
    
    const userId = app.auth().currentUser.uid;
    const itemId = paymentList.id;
    const value =
      convertToNumber(paymentList.signature_value) ||
      convertToNumber(paymentList.valorPreco);

    const dataPayment = {
      id,
      userId,
      itemId,
      name: nome.value,
      value: parseInt(value)
    };

    
    if (isSignature) {
      paySignature(dataPayment, (resp,error) => {
        if (resp) {
          console.log('aqui');
          updateSignature(true);
          removeToCart()
          removeToPayment()
          setRouteToStack('/pagamento');
          toast.success("Pagamento feito com sucesso", optionsToast);
          history.push(`/ConfirmCompra/${paymentList.id}`);
        } 
        else if(error){
          //Tratar Erro no cb
          toast.error("Ooops, algo deu errado", optionsToast);
          console.log(error);
        }
      });
    } else {
      //spectacle
      paySpectacle(dataPayment, (resp, error) => {
        if (resp) {
          removeToCart()
          removeToPayment()
          setRouteToStack('/pagamento');
          toast.success("Pagamento feito com sucesso", optionsToast);
          history.push(`/ConfirmCompra/${paymentList.id}`);
        } else if(error){
          //Tratar Erro no cb
          toast.error("Ooops, algo deu errado", optionsToast);
          console.log(error.response);
        }
      });
    }
  };
  useEffect(() => {
    isDisabled();
  }, [error]);

  function isDisabled() {
    const fields = ["nome", "cpf"];
    if (isFillFields()) {
      return fields.some((field) =>
        error[field] !== "isValid" ? true : false
      );
    } else {
      return true;
    }
  }

  function isFillFields() {
    return Object.keys(error).length > 0 ? true : false;
  }

  return (
    <>
    <Header />
      <ToastContainer/>
      <S.Container>
        <S.Span size="3">Pagamento</S.Span>

        <S.Detalhes>
          <S.DetalhesContainer>
            <S.Span size="1">Detalhes da compra</S.Span>
            <S.DetalhesRow>
              <S.Span size="1">Valor:</S.Span>
              <S.Span size="1.2" color="#fdd">
                {(paymentList.value && currency(paymentList.value)) ||
                  (paymentList.valorPreco && currency(paymentList.valorPreco))}
              </S.Span>
            </S.DetalhesRow>
          </S.DetalhesContainer>
        </S.Detalhes>

        {
          isSignature &&

        <S.Form onSubmit={(e) => handleSubmitAddress(e)}>
          <S.DetalhesContainer>
            <S.Span size="1">Dados do Endereço</S.Span>
          </S.DetalhesContainer>

          <S.Row>
            <GetAddressByCep             
              setAddress={setAddress}
              address={address}
              setCep={setCep}
              cep={cep}
            >
            </GetAddressByCep>
          </S.Row>

          <S.Row>
            <S.Input 
              className="form-control"
              placeholder="Rua..."
              value={address}
              onChange={(e) => setAddress(e.target.value)}
            />

            
          </S.Row>

          <S.Row>
            <S.Input 
              className="form-control"
              placeholder="Número"
              value={number}
              onChange={(e) => setNumber(e.target.value)}
              style={{width: "20%"}}
            />
            <S.Input 
              className="form-control"
              placeholder="Complemento"
              value={complement}
              onChange={(e) => setComplement(e.target.value)}
            />
            
          </S.Row>
       
          <S.ContainerActions>
            <S.Button type="submit" disabled={isDisabled}>Confirmar</S.Button>
          </S.ContainerActions>
        </S.Form>
        }

        <S.Form onSubmit={(e) => handleSubmit(e)}>
          <S.DetalhesContainer>
            <S.Span size="1">Dados do Pagamento</S.Span>
          </S.DetalhesContainer>
          <S.Row>
            <div className="group position-relative">
              <S.Input
                className="form-control"
                placeholder="Nome"
                // required
                // value={nome}
                name="nome"
                // onChange={(e) => {
                //   handleChangeNome(e);
                // }}
                onBlur={(e) => validate(e, { required: true, min: 3 })}
              />
              <div
                className={`tooltip-error ${
                  error["nome"] && error["nome"] !== "isValid" ? "show" : ""
                }`}
              >
                {error["nome"]}
              </div>
            </div>
          </S.Row>
          <S.Row>
            <div className="group position-relative">
              <S.Input
                className="form-control"
                placeholder="CPF"
                onChange={(e) => setCpf(e)}
                required
                mask="999.999.999-99"
                maskChar={null}
                name="cpf"
                onBlur={(e) => validate(e, { required: true, cpf: true })}
              />
              <div
                className={`tooltip-error ${
                  error["cpf"] && error["cpf"] !== "isValid" ? "show" : ""
                }`}
              >
                {error["cpf"]}
              </div>
            </div>
          </S.Row>
          <S.Row>
          <fieldset className="FormGroup">
              <div className="FormRow">
                <CardElement options={CARD_OPTIONS} />
              </div>
            </fieldset>
            </S.Row>
          <S.ContainerActions>
            <S.ButtonVoltar onClick={() => history.goBack()}>
              <i className="bi bi-chevron-left"></i>Voltar
            </S.ButtonVoltar>
            <S.Button type="submit" disabled={isDisabled}>PAGAR</S.Button>
          </S.ContainerActions>
        </S.Form>
      </S.Container>
    <Footer />
    </>
  );
}

export default Pagamento;
