import styled from 'styled-components';

import assentos from '../../assets/novorodape.png'

import { SCREEN_SIZES } from '../../utils/screen';

export const Container = styled.footer`
  display: flex;
  flex-direction: column;
  padding-top: 0vh;
  align-items: center;
  height: auto;
  width: 100%;
  background: #000;
  background-image: url(${assentos});
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100% auto;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    padding-top: 30px;
  }
`;

export const Content = styled.div`
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 160px;
  @media (max-width: ${SCREEN_SIZES.sm}) {
    flex-direction: column;
    align-items: center;
    padding-bottom: 0px;
  }
`;


export const BoxPG = styled.div`
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  
  h3 {
    font-weight: normal;
    font-size: 14px;
  }

  @media (max-width: ${SCREEN_SIZES.sm}) {
    align-items: center;
    margin-bottom: 20px;
    order: 2;
    padding: 25px;
  }
`;


export const BoxRed = styled.div`
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  
  h3 {
    font-weight: normal;
    font-size: 14px;
  }

  @media (max-width: ${SCREEN_SIZES.sm}) {
    align-items: center;
    margin-bottom: 20px;
    padding: 25px;
    order: 1;
  }
`;


export const BoxDoe = styled.div`
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  
  h3 {
    font-weight: normal;
    font-size: 14px;
  }
  margin-top: 8vh;
  @media (max-width: ${SCREEN_SIZES.sm}) {
    margin-top: 1vh;
    align-items: center;
    margin-bottom: 20px;
    padding: 25px;
    order: 3;
  }

  @media (max-width: 425px) {
    margin-top: 1vh;
    align-items: center;
    margin-bottom: 20px;
    padding: 25px;
    order: 3;
    margin-top: 0vh !important;
  }
`;


export const Stores = styled.div`
  display: flex;
  margin-top: 10px;
  align-self:center;
  cursor: initial;

  img {
    width: 30px;
    transition: filter 0.2s;
    &:hover {
      filter: brightness(80%);
    }
  }

  .apple {
      
      margin-right: 21px;
    }
`;

export const Social = styled.div`
  display: flex;

  gap: 5px;
  
  align-self: center;  
  img {
    width: auto;
    height: 45px;
    margin-right: 5px;
    cursor: pointer;

    transition: filter 0.2s;

    &:last-child {
      margin-right: 0;
    }
    
    &:hover {
      filter: brightness(80%);
    }
  }
`;

export const Line = styled.div`
  display: flex;
 
  align-items: center;

  img {
    margin-right: 15px;

    &:last-child {
      margin-right: 0;
    }
  }
`;

export const Mercadata = styled.div`
  @import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');
  color: #fff;
  position: relative;
  top:18vh;
  display: flex;
  align-items: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 12px;

  svg {
    margin: 0 5px;
  }
  @media (max-width: 415px) {
    margin: 0;
    padding-bottom: 0vh !important;
    top:0vh;
  }
  @media (max-width: ${SCREEN_SIZES.sm}) {
    margin: 0;
    padding-bottom: 0vh;
    top:0vh;
  }
`;

export const Info = styled.div`
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-bottom: 5vh;
  align-items: center;
 
 
  @media (max-width: ${SCREEN_SIZES.sm}) {
    flex-direction: column;
    align-items: center;
  }
`;

export const Span = styled.span`
cursor: pointer;
border-radius: 10px;

background-color:${props => props.bg} ;
color: ${props => props.cor};

`
export const SpanDoe = styled.h3`
display: flex;
flex-direction:column;
justify:center;
align-items:center;
cursor: pointer;
font-size: ${props => props.size + "rem"};
color: ${props => props.cor};

`

export const imglogo = styled.img`
 
  width:10vw ;
  margin-top:3vh ;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width:30vw ;

  }
`