import styled from "styled-components";

import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.div`
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 15vh;
  margin-bottom: 14vh;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    flex-direction: column;
    margin-top: 15vh;
  }
`;

export const AreaPerfil = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50vw;

  form {
    width: 100%;
  }

  input {
    height: 50px;
    font-size: 20px;
  }

  @media (max-width: ${SCREEN_SIZES.sm}) {
    padding-top: 0px;
    width: 100vw;
    align-items: center;
    height: auto;
  }
`;

export const TextoLateral = styled.div`
  display: flex;
  flex-direction: column;
  width: 14%;
  justify-content: flex-start;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    flex-direction: row;
    width: 100vw;
    height: 80px;
    justify-content: space-evenly;
  }
`;
export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};

  :hover {
    color: #fdcc12;
  }

  @media (max-width: ${SCREEN_SIZES.sm}) {
    display: flex;
    flex-grow: 1;
  }
`;

export const SpanLatera = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
  border-radius: 10px;
  padding: 15px;
  background-color: ${(props) =>
    props.setadoPerfil === true ? "#FDCC12" : "none"};
  color: ${(props) => (props.setadoPerfil === true ? "black" : "white")};

  cursor: pointer;
  :hover {
    background-color: #fdcc12;
    color: black;
  }
`;

export const SpanCompra = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
  font-weight: bold;
  border-radius: 10px;
  padding: 15px;
  background-color: ${(props) =>
    props.setadoCompra === false ? "#FDCC12" : "none"};
  color: ${(props) => (props.setadoCompra === false ? "black" : "white")};
  :hover {
    background-color: #fdcc12;
    color: black;
  }
  cursor: pointer;

  @media (max-width: ${SCREEN_SIZES.sm}) {
  }
`;

export const AreaFotoPerfil = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 200px;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    display: none;
  }
`;

export const AreaFotoPerfil1 = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 150px;

  @media (min-width: ${SCREEN_SIZES.sm}) {
    display: none;
  }
`;

export const Foto = styled.div`
  display: flex;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  background: white;
  border-radius: 50%;
`;

export const GrupoInput = styled.div`
  margin: 5px;
`;
export const SpanInput = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: 10px;
  font-size: ${(props) => props.size + "px"};
`;

export const Input = styled.input`
  width: 100%;
  height: 35px;
  margin: 5px;
  padding: 10px;
  font-size: 13px;
`;

export const Button = styled.button`
  background-color: #fdcc12;
  color: black;
  text-transform: uppercase;
  font-weight: bold;
  width: 80%;
  border: none;
  border-radius: 5px;
  :hover {
    background-color: #ad8e1c;
  }
`;

export const A = styled.button`
  border: none;
  background-color: transparent;
  text-decoration: none;
  color: white;
  :hover {
    color: #fdcc12;
  }
`;

export const AreaCompras = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50vw;
  height: auto;
  padding-top: 0px;
  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 100vw;
  }
`;

export const Carrinho = styled.div`
  display: flex;
  width: 50vw;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 10px;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 100vw;
  }
`;

export const CarrinhoItem = styled.div`
  display: flex;
  height: auto;
  width: 25%;
  flex-direction: column;
  margin-left: 10%;
`;

export const Img = styled.img`
  width: 150px;
  height: 100%;
  float: left;
  border-radius: 8px;
`;
export const X = styled.div`
  display: flex;
  width: 2%;
  height: 150px;
`;

export const Info = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
  height: 60vh;
  overflow-y: scroll;
  overflow-x: hidden;
`;

export const BordarBottom = styled.div`
  border-bottom: 1px solid white;
  width: 100%;
  height: 20px;
`;

export const Space = styled.div`
  position: relatives;
  width: 500px;
  padding-top: 800px;
  height: 600px;
`;
export const ImgPlano = styled.img`
  width: 150px;
  height: 90%;
  float: left;
  background: #1b1b1b;
  object-fit: cover;
  border-radius: 10px;
`;
