import styled from "styled-components";
import { SCREEN_SIZES } from "../../utils/screen";

export const PopapDiv1 = styled.div`
  width: 50%;
  min-height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #fdcc12;
  border-radius: 15px;
  padding: 20px 10px;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 80%;
  }
`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: ${(props) => props.size || "0.7rem"};

  text-align: justify;

  form {
    display: flex;

  }
`;

export const PopapButton = styled.button`
  height: 5vh;
  background-color: #000;
  border-radius: 15px;
  border: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px 10px;

  :hover {
    padding: 10px;
  }
`;

export const PopapDiv = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.6);
  z-index: 99;
`;

export const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
`;

export const Container = styled.div`
  // height: ${(props) => (props.noItem ? "100vh" : "auto")};
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1200px;
  width: 100%;
  flex-direction: column;
  padding: 10px;
  // @media (max-width: ${SCREEN_SIZES.md}) {
  //   max-width: 1000px;
  // }

  // @media (max-width: ${SCREEN_SIZES.sm}) {
  //   max-width: 680px;
  //   padding: 20px;
  // }
`;

export const Header = styled.div`
  display: flex;
  width: 100%;
  margin-top: 2vh;
  justify-content: center;
  justify-items: center;
`;

export const HeaderSpan = styled.span`
  font-size: 1.3rem;
  width: 100%;
  margin-bottom: 2vh;
  text-align: center;
  font-weight: bold;
  color: #fff;
  z-index: 1;
`;

export const CartActions = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding-bottom: 10px;
  margin-bottom: 30px;
  gap: 1vw;
  button {
    background: linear-gradient(
      to bottom,
      black 0%,
      black 23.7%,
      #383428 23.8%,
      #383428 24%,
      black 24.5%,
      black 70%,
      #fdcc12 70%,
      #fdcc12 100%
    );
    color: black;
    border-radius: 8%;
    font-size: 1rem;
    text-transform: initial;
  }
  .valorContainer {
    display: flex;
    flex-direction: row;
    align-items: start;
    justify-content: center;
  }
  .valorPlano {
    font-size: 3rem;
    font-weight: bold;
    color: #fff;
    display: flex;
    flex-direction: column;
  }
  .valorPequeno {
    font-size: 1rem;
    font-weight: 100;
    margin-top: 2.5vh !important;
  }
  .plano {
    font-size: 1.5rem;
    font-weight: 600;
    color: #fff;
  }
  button:hover {
    background: linear-gradient(
      to bottom,
      black 0%,
      black 23.7%,
      #383428 23.8%,
      #383428 24%,
      black 24.5%,
      black 70%,
      #d7ac02 70%,
      #d7ac02 100%
    );
    color: #212121;
    filter: brightness(120%) saturate(4);
  }

  @media (max-width: 700px) {
    flex-direction: column;
  }
`;

export const ButtonPlanos = styled.button`
  background-color: #fdcc12;
  color: black;
  text-transform: uppercase;
  width: 150px;
  height: 50px;
  transition: all 0.3s;
  border: none;
  margin: 0 !important;
  border-radius: 5px;
  &:hover {
    background-color: #d7ac02;
  }
`;

export const ButtonPagar = styled(ButtonPlanos)`
  width: 30vw;
  position: relative;
  font-weight: bolder;
  z-index: 1;
  &:hover {
    filter: brightness(120%) saturate(4);
  }
`;

export const ButtonVoltar = styled.button`
  text-transform: uppercase;
  font-size: 1rem;
  border: none;
  color: #fff !important;
  transition: all 0.3s;
  background: transparent !important;
  &:hover {
    color: #d7ac02;
  }
`;

// ! ###################################### NEW STYLES ######################################

export const Hr = styled.div`
  position: relative;
  right: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  height: 1px;
  transition: all 0.5s;
`;

export const HoldingContainer = styled.div`
  cursor: pointer;
  display: flex;

  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;

  position: relative;
  z-index: 2;
  color: #fff;
  background-color: #000;
  border-radius: 15px;

  height: 250px !important;
  width: 100%;
  max-width: 250px;

  &:hover .line {
    background-color: #fdcc12;
  }

  &:hover {
    &::before {
      background-color: #c99f08;
      transition: 0.5s;
    }
  }

  &::before {
    content: "";
    position: absolute;
    background-color: #fdcc12;
    height: 40px;
    width: 100%;
    bottom: 0%;
    border-radius: 0px 0px 15px 15px;
    cursor: pointer;
    transition: all 0.2s;
  }

  .signature-text {
    position: absolute;
    bottom: -8px;
    color: #000;
    cursor: pointer;
    font-weight: 700;
  }

  @media (max-width: 700px) {
    max-width: 250px !important;
    margin-bottom: 25px;
  }
`;

export const SignatureDuration = styled.div`
  width: 100%;
  font-style: Ubuntu;
  font-size: 1rem;
  display: flex;
  flex-wrap: no-wrap;
  justify-content: center;
  align-items: flex-start;
`;

export const SignaturePrice = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  s {
    font-size: 1.5rem !important;
  }
  letter-spacing: -2px;
  .texto {
    // margin-bottom: 2vh;
  }
`;

export const AssignButton = styled.button`
  border-radius: 12px;
  width: 80%;
  justify-self: flex-end;
  outline: 0;
  border: 0;
  background-color: #fdcc12;
`;
