import React, {useState} from "react";
import * as S from "./styles";
import app from "../../auth/config";
import { useHistory } from "react-router-dom";
import "./style.css";
import Layout from "../../layout";
import Img from "../../assets/imagemAssinatura.png";
import { usePlans } from "../../dados/context/PlansContext";
import { usePayments } from "../../dados/context/PaymentContext";
import { useAuth } from "../../auth/auth";
import { useCarts } from "../../dados/context/CartContext";
import SignatureCard from "../../components/SignatureCard";

function Assinatura() {

  const [showModal3d, setShowModal3d] = useState(false);
  const [numVr, setNumVr] = useState(1)

  const history = useHistory();
  const { plans } = usePlans();
  const { addToCart } = useCarts();
  const { updateSignature, userIsAuthenticated, signature, setRouteToStack } =
    useAuth();
  const { payFree } = usePayments();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";
  const isSignature = userIsAuthenticated() && signature?.isActive;
  const planFree = plans.find((plano) => plano.is_test_period === true);

  function handlePlan3d() {

    let yearlyPlan = plans.filter(plan => plan.name === "Anual")

    yearlyPlan[0].value = 19178 + (numVr * 3511)

    addToCart({
      ...yearlyPlan[0],
      signature_id: yearlyPlan[0].id,
      signature_value: yearlyPlan[0].value,
      num_vr: numVr
    });
    setRouteToStack("/");
    history.push("/cart");

  }

  return (
    <Layout>
      {isSignature ? (
        history.push("/espetaculos/assinante")
      ) : (
        <S.Wrapper>
          <S.Container>
            <>
              <S.HeaderSpan style={{fontSize:'1.5rem'}}>Teste grátis por 7 dias</S.HeaderSpan>
              
              <S.HeaderSpan style={{color: "#FDCC12"}}>Assista onde quiser, cancela quando quiser*</S.HeaderSpan>
              
              <S.HeaderSpan>Escolha o melhor plano para você:</S.HeaderSpan>

              <S.CartActions>
                <S.ButtonVoltar onClick={() => history.push(`/`)}>
                  <i className="bi bi-chevron-left"></i>Voltar{" "}
                </S.ButtonVoltar>
                {plans?.map((plano) => {
                  //reordena do menor para o maior
                  // plans.sort(function(a, b){return b.value - a.value})
                  return (
                    <S.HoldingContainer
                      className="buttonPagar"
                      key={plano.id}
                      onClick={() => {
                        if (!isSignature) {
                          if (plano.name === "Anual") {
                            setShowModal3d(true)
                          }

                          addToCart({
                            ...plano,
                            signature_id: plano.id,
                            signature_value: plano.value,
                          });
                          setRouteToStack("/Assinatura");
                          history.push("/cart");
                        } else {
                          history.push("/espetaculos/assinante");
                        }
                      }}
                    >
                      <S.SignatureDuration className="plano">
                        {" "}
                        {plano.name}
                      </S.SignatureDuration>
                      <S.Hr className="line"></S.Hr>
                      <S.SignaturePrice className="valorPlano">
                        {/* inclui o valor riscado acima do preço do plano */}
                        {plano.name === "Mensal" ? (
                          <s
                            style={{ textDecoration: "line-through" }}
                            className="texto"
                          >
                            R$29,96
                          </s>
                        ) : plano.name === "Trimestral" ? (
                          <s
                            style={{ textDecoration: "line-through" }}
                            className="texto"
                          >
                            R$79,91
                          </s>
                        ) : plano.name === "Anual" ? (
                          <s
                            style={{ textDecoration: "line-through" }}
                            className="texto"
                          >
                            R$297,52
                          </s>
                        ) : (
                          <div></div>
                        )}

                        <div className="valorContainer">
                          <div className="valorPequeno">
                            {plano?.breakValue?.currency}
                          </div>
                          {plano?.breakValue?.integer}
                          <div className="valorPequeno">
                            ,{plano?.breakValue?.decimal}
                          </div>
                        </div>
                        {/* inclui o texto abaixo do preço do plano */}
                        {plano.name === "Mensal" ? (
                          <s className="texto" style={{ color: "transparent" }}>
                            . <div style={{color: "transparent"}}>.</div>
                          </s>
                        ) : plano.name === "Trimestral" ? (
                          <s className="texto free-trial">+ 7 dias grátis <div style={{color: "transparent"}}>.</div></s> 
                        ) :  plano.name === "Anual" ? (
                        <s className="texto free-trial">+ 7 dias grátis <div style={{color: "#FDCC12"}}>+ óculos VR</div></s>
                        ) : (
                          <div></div>
                        )}
                      </S.SignaturePrice>
                      <br />
                      <p className="signature-text">ASSINE JÁ!</p>
                    </S.HoldingContainer>
                  );
                })}
              </S.CartActions>

              <S.HeaderSpan style={{fontSize:'1rem'}}>*Cancelamento até o sétimo dia após a compra.</S.HeaderSpan>
            </>
            {/* <S.CartInfo>
              <span>
                Torne-se um assinante e garanta 50% de desconto neste conteúdo
              </span>
              <S.CartInfoText>
                E ainda tenha acesso a um amplo conteúdo em peças teatrais,
                shows musicais e palestras para assistir a qualquer hora(exceto
                lançamentos).
              </S.CartInfoText>
            </S.CartInfo> */}
          </S.Container>
        </S.Wrapper>
      )}
      {showModal3d &&
    
    <S.PopapDiv>
      <S.PopapDiv1 >

        <span style={{cursor: "pointer", float: "right", marginRight: "-90%"}} onClick={() => setShowModal3d(false)} size="1.5">X</span>

        <S.Span style={{fontWeight: 'bold'}} cor="black" size="2">
          Promoção 3D 
        </S.Span>

        <S.Span cor="black" size="1.8" >
        
          Na compra da assinatura anual você ganha um óculos VR.
          
        </S.Span>

        <S.Span cor="black" size="1.8" >
          
          Após a compra você receberá o óculos VR para seu uso em regime de comodato ficando sob sua responsabilidade. Uso exclusivo para as peças em visualização VR do PalcoWeb. Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação.
          
        </S.Span>

        <S.Span cor="black" size="1.8">
          Deseja comprar mais óculos VR?
        </S.Span>

        <S.Span cor="black" size="1.8">
          Selecione o N° de óculos
        </S.Span>

        <select onChange={(e) => setNumVr(e.target.value)}>
          <option value={1}>1</option>
          <option value={2}>2</option>
          <option value={3}>3</option>
        </select>

        <div style={{display: "flex", justifyContent: "space-between"}}>
          
          {/* <S.PopapButton onClick={() => window.open('https://produto.mercadolivre.com.br/MLB-1688483109-oculos-realidade-virtual-3d-compativel-com-todos-smartphones-_JM')}>
            <S.Span size="1.5">Comprar Óculos VR</S.Span>
          </S.PopapButton> */}
          
          <S.PopapButton onClick={() => handlePlan3d()}>
            <S.Span size="1.5">Assinatura Anual</S.Span>
          </S.PopapButton>
          
        </div>

        <b>Não nos responsabilizamos pelo uso inadequado do produto.</b>

      </S.PopapDiv1>
    </S.PopapDiv>}
    </Layout>
  );
}

export default Assinatura;
