import React from "react";
import { SwiperSlide } from "swiper/react";
import { useAuth } from "../../auth/auth";
import ButtonContinuar from "../../components/ButtonContinuar";
import Card from "../../components/Card";
import Footer from "../../components/Footer";
import Header from "../../components/Header";
import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import * as S from "./styles";

function AssinaturaConteudo() {
  var showsSignaturess = []
  const {
    showsSignatures
  } = useShows();
  showsSignaturess.push(showsSignatures)
  const { signature, userIsAuthenticated } = useAuth();

  return (
    <>
      <Header />

      <S.Container>
        {showsSignaturess?.map((show) => (
          <Section key={show.id} title={show.name} filter>
            <HorizontalList id="emAlta">
              {show.spectacle &&
                show.spectacle.length > 0 &&
                show.spectacle.map((card) => {
                  if (card.isActiveSignature === true) {
                    return (
                      <SwiperSlide key={String(card.id)}>
                        {card === "" ? (
                          <Card noContent />
                        ) : (
                          <S.CardTest>
                            <S.Img
                              src={card.thumbnail}
                              style={{
                                position: "absolute",
                              }}
                              value={card.id}
                              alt="imagem referente a peça"
                            />

                            <S.InfoCard>
                              <S.InfoCardConteudo>
                                <S.Classificacao>
                                  {card.ageRating}
                                </S.Classificacao>

                                <S.Compartilhar>
                                  <i className="bi bi-share-fill"></i>
                                </S.Compartilhar>
                              </S.InfoCardConteudo>

                              <S.Titulo>{card.title}</S.Titulo>
                              <S.ConteudoCard
                                style={{
                                  textTransform: "uppercase",
                                }}
                              >
                                {show.name}
                              </S.ConteudoCard>
                              <S.Sinopsia>{card.synopsis}</S.Sinopsia>

                              <ButtonContinuar
                                title="Assistir"
                                spectacle={card}
                                toPlan={
                                  card.isActiveOnDemand
                                    ? "onDemand"
                                    : "signature"
                                }
                                userIsSignature={
                                  userIsAuthenticated() && signature?.isActive
                                    ? true
                                    : false
                                }
                                userIsAuth={
                                  userIsAuthenticated() ? true : false
                                }
                              />
                            </S.InfoCard>
                          </S.CardTest>
                        )}
                      </SwiperSlide>
                    );
                  }
                  return 0;
                })}
            </HorizontalList>
          </Section>
        ))}
      </S.Container>
      <Footer />
    </>
  );
}

export default AssinaturaConteudo;
