import styled from "styled-components";

import { SCREEN_SIZES } from "../../utils/screen";

export const PopapDiv1 = styled.div`
  width: 50%;
  min-height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #fdcc12;
  border-radius: 15px;
  padding: 20px 10px;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 80%;
  }
`;

export const Span = styled.span`
  color: ${(props) => props.cor || "white"};
  font-size: ${(props) => props.size || "0.7rem"};

  text-align: justify;

  form {
    display: flex;

  }
`;

export const PopapButton = styled.button`
  height: 5vh;
  background-color: #000;
  border-radius: 15px;
  border: none;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px 10px;

  :hover {
    padding: 10px;
  }
`;

export const PopapDiv = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0,0,0,0.6);
  z-index: 99;
`;

export const Container = styled.div`
  color: white;
  width: 100vw;
  background: #1b1b1b;
  /* padding-bottom: 50px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

export const PwBanner = styled.img`
  width: 40vw;
  margin: 20px;
  @media (min-width: 900px) {
      width:20vw;
  }
`;

export const BtnNoStyle = styled.button`
  background:transparent; 
  color:#FDCC12; 
  text-decoration: underline; 
  font-Size: large; 
  cursor: pointer; 
  border:none;

  @media (min-width: 900px) {
      font-Size: x-large; 
  }
`;
///tudo sobre card

export const DescriptionContainer = styled.div`
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  hr {
    width: 60%;
    color: rgba(255, 255, 255, 0.5);
  }

  h2 {
    font-family: Ubuntu;
    font-size: 22px;
    font-weight: 700;
    line-height: 20px;
    letter-spacing: 0.0025em;
    text-align: center;
  }

  p {
    font-family: Ubuntu;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.0025em;
    text-align: center;
  }

  span {
    font-family: Ubuntu;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 0.0025em;
    text-align: center;
    color: #FDCC12;
  }

  div {
    display: flex;

    button {
      margin: 15px 5px;
      background-color: #FDCC12;
      border: none;
      border-radius: 6px;
      padding: 10px 15px;

      font-family: 'Ubuntu';
      font-style: normal;
      font-weight: 700;
      font-size: 13px;
      line-height: 15px;
      letter-spacing: -0.005em;

      color: #000000;

    }
  }
  @media (min-width: 900px){
    h2 {
    font-size: 42px;
    line-height: 30px;
    }

    p {
      font-size: 32px;
    }

    span {
      font-size: 32px;
    }

    div {
      button {
        padding: 20px 30px;
        font-size: 26px;
      }
    }
  }
`;

export const Card = styled.div`
  width: 30vw;
  height: 200px;
  color: white;

`;


