import * as S from "./styles";
import { useHistory } from "react-router-dom";
import Summary from "../Summary/summary";
import React from "react"
import Button from "../Button";

export default function DuvidasFrequentes() {
    const history = useHistory();
    return (
        <>
            <S.TitleDuvidas size="20">Dúvidas frequentes</S.TitleDuvidas>
            <Summary title="O que é o Palco Web?" texto="O PalcoWeb é uma plataforma digital que tem como propósito oferecer serviço de streaming de qualidade para conteúdos culturais Made in Brasil para o Mundo e para Todos."></Summary>
            <Summary title="Precisa pagar para se cadastrar no Palco Web?" texto="Não. Basta criar um cadastro para navegar na plataforma, temos produtos que são disponibilizados gratuitamente."></Summary>
            <Summary title="Quais as vantagens do Palco web? #AGORAPODE" texto="Com o PalcoWeb você pode comer, beber, comentar idéias com amigos, tomar conta do seu pimpolho, e até atender chamadas de celular enquanto vc assiste uma peça ou um show no conforto do seu lar.
                Para as produtoras é possível fazer uma apresentação presencial numa cidade ou região e transmitir online para o resto do Brasil e do mundo."></Summary>
            <Summary title="Para quem é o Palco Web?" texto="Para todos amantes de cultura que não dispensam o custo beneficio,e ainda assim agregar cultura em seu cotidiano."></Summary>
            <Summary title="Tem apenas teatro no Palco Web?" texto="Nós do palco web temos o intuito de enriquecer nossa plataforma com diversas manifestações culturais audiovisuais, seja com teatro, shows ou ate mesmo palestras."></Summary>
            <Summary title="O que é  TV Palco Web?" texto="Programa de entrevista e palestras com diretores, produtores e artistas da área de teatro e cultural."></Summary>
            <Summary title="PalcoWeb em óculos VR?" texto="Em breve lançaremos peças em realidade virtual, implementando futuramente nossa interação com o metaverso cultural do PalcoWeb."></Summary>

            {/* <S.Span size="20" className="pt-5">Tive problemas</S.Span>
            <Summary title="Problemas com login" texto="Fale conosco no formulário abaixo."></Summary>
            <Summary title="Problemas com pagamento" >
            <br />
            •Verifique se o seu cartão é de uma das bandeiras <br />
            •Certifique-se que os dados do cartão estão preenchidos corretamente<br />
            •Proceda com o pagamento.<br />
            •Não deu certo? Entre em contato abaixo que iremos responder o mais breve possível.
            </Summary>
            <Summary title="Problemas na platafomra" texto="Fale conosco no formulário abaixo." ></Summary>
            <Summary title="Cancelamento" texto="Fale conosco no formulário abaixo." ></Summary> */}
            <S.BtnNoStyle onClick={() => history.push('/SaibaMais')} >ver mais perguntas</S.BtnNoStyle>
            <br/>
            <hr style={{ width: "60vw", color: "rgba(255, 255, 255, 0.5)"}}/>
            <Button
                   onClick={() => history.push(`/Assinatura`)}
                  text={"Assine já"}
            />
        </>
    )
}