import React from 'react'
import './style.css'
import * as S from "./styles";
import pix5pila from "../../assets/pix5pilas.jpg";
import pix10pila from "../../assets/pix10pilas.jpg";
import pix15pila from "../../assets/pix15pilas.jpg";
import { useState, useEffect } from "react"

export default (props) => {
    useEffect(() => {
      document.getElementById("qrcode").style.display = "none"
    },[])
    const [image, setImage] = useState("")
    function generatePix(image) {
      setImage(image)
      document.getElementById("qrcode").style.display = "inline";
    }
    return (
      <>
        <S.CardPix>
          <S.CardPixItem justify="end">
            <S.Span cor="white" className="p-5">
              <button
                onClick={() => props.setabrirpix(false)}
                className="btn btn-warning"
              >
                <i className="bi bi-x-lg"></i>
              </button>
            </S.Span>
          </S.CardPixItem>
          <S.CardPixItem justify="center">
            {" "}
            <S.Span size="1.2">Faça um pix para o Retiro dos artistas</S.Span>
          </S.CardPixItem>
          <S.CardPixItem justify="center" id="button-pix">
            <div>
              <S.Span size="1">Valores Sugeridos:</S.Span>
              <button
                className="btn btn-warning"
                onClick={() => generatePix(pix5pila)}
              >
                PIX 5 REAIS
              </button>

              <button
                className="btn btn-warning"
                onClick={() => generatePix(pix10pila)}
              >
                PIX 10 REAIS
              </button>

              <button
                className="btn btn-warning"
                onClick={() => generatePix(pix15pila)}
              >
                PIX 15 REAIS
              </button>
            </div>
          </S.CardPixItem>
          <S.CardPixItem justify="center">
            <center>
              <div id="qrcode">
                <img alt="" src={image} />
              </div>
            </center>
          </S.CardPixItem>
        </S.CardPix>
      </>
    );
}