import React, { useState } from 'react';
import "./style.css";

export default function Summary(props) {

    const [open, setOpen] = useState(false)

    return (
        <details className="faq" >
            <summary className="summary" onClick={() => setOpen(!open)}><span className='p-1'>{open ? <i className="bi bi-caret-down-fill"></i> : <i className="bi bi-caret-right-fill"></i>}
            </span>
               
                    {props.title}
                     
            </summary>
            <p className='m-3'>{props.texto}
            {props.children}  </p>
        </details>
    )
}