import React from 'react';

import { Container } from './styles';

function SpinnerCustom({variant}) {
  return (
    <Container>
      <div className="spinner-border text-warning" role="status"  style={variant==='md'?({height: "20px", width: "20px" }):({height: "80px", width: "80px" })}/>
    </Container>
  );
}

export default SpinnerCustom;