import styled from "styled-components";

import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.div`
  width: 100vw;
  background: black;
  display: flex;
  flex-direction: row;
  position: relative;
  background-color: #1d1d1d;
  margin-bottom: 30px;
  /* @media (max-width: ${SCREEN_SIZES.md}) {
    flex-direction: column;
  } */
  @media (max-width: ${SCREEN_SIZES.md}) {
    width: 100vw;

    flex-direction: column;
  }
`;
///tudo
export const ClassificacaoCard = styled.span`
  width: 25px;
  height: 20px;
  background-color: white;
  color: black;
  padding: 0px;
  border-radius: 5px;
  margin: 5px;
  font-size: 0.8rem;

  text-align: center;
  justify-content: center;
  align-items: center;
`;
export const ButtonVoltar = styled.span`
  font-size: 1.8rem;
  color: #fdcc12;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 50px;
  @media (max-width: ${SCREEN_SIZES.md}) {
    right: 20px;
  }
`;

export const ContainerImg = styled.div`
  width: 65vw;
  height: 100%;
  position: absolute;
  margin-bottom: 0px;
  @media (max-width: ${SCREEN_SIZES.md}) {
    max-width: 100vw;
    height: 80vw;
    position: relative;
  }
  iframe {
    margin-top: 0px;
    padding: 0;
    width: 65vw;
    height: 100%;
    @media (max-width: ${SCREEN_SIZES.md}) {
      width: 100vw;
    }
  }
  /* &:after{
 content: '';
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 height: 100%;
 background:linear-gradient(0.25turn, rgba(0,0,0,.9), rgba(0,0,0,.2), rgba(0,0,0,.4));
} */
`;

export const ButtonContinuar = styled.button`
  background-color: #fdcc12;
  color: black;
  font-weight: bolder;
  text-transform: uppercase;
  margin: 0 !important;
  :hover {
    background-color: #fdcc12;
  }
`;

export const Thumbnail = styled.img`
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;

  @media (max-width: ${SCREEN_SIZES.md}) {
  }
`;

export const Conteudo = styled.div`
  width: 40vw;
  height: 100%;
  padding: 40px;
  display: flex;
  margin-left: 65vw;
  flex-direction: column;
  align-items: center;
  background: #1d1d1d;

  /* @media (max-width: ${SCREEN_SIZES.md}) {
    width: 70%;
    padding: 30px;
  } */
  @media (max-width: ${SCREEN_SIZES.md}) {
    width: 100vw;
    padding: 20px;
    margin-left: 0;
  }
`;

