import React, { createContext, useContext, useEffect, useState } from "react";
import PlansServices from "../../Connections/plans";
import { formatPlan, formatPlans } from "../../utils/dtos/plans";

const Plans = createContext({});

export function PlansProvider(props) {
  const [plans, setPlans] = useState([]),
    [plan, setPlan] = useState({});

  useEffect(() => {
    getPlan();
  }, []);

  const getPlan = async () => {
    try {
      const response = await PlansServices.loadPlans();
      const plans = formatPlans(response.data?.body);
      setPlans(plans);
    } catch (error) {
      console.log(error);
    }
  };

  const getPlanById = async (id) => {
    try {
      const response = await PlansServices.getPlanById(id);
      const plan = formatPlan(response.data?.body);
      setPlan(plan);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Plans.Provider
      value={{
        plan,
        plans,
        getPlanById,
        getPlan
      }}
    >
      {props.children}
    </Plans.Provider>
  );
}

export const usePlans = () => useContext(Plans);
