import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
// import { SwiperSlide } from "swiper/react";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";
import Banner from "../../components/Banner";
// import Card from "../../components/Card";
// import Share from "../../components/CardCompartilhar/index";
// import HorizontalList from "../../components/HorizontalList";
import Section from "../../components/Section";
import { useShows } from "../../dados/context/ShowsContext";
import Layout from "../../layout";
// import * as SCard from "../Canais/styles";
// import TelaCardezao from "../Cardezao/index";
import * as S from "./styles";
import logo from "../../assets/logoPalcoweb.png";
import TvPw from "../../assets/logoTV_PalcoWeb.png";
import SpectaclesCarousel from "../../components/SpectaclesCarousel";
import DuvidasFrequentes from "../../components/DuvidasFrequentes";
import ChannelsCarousel from "../../components/ChannelsCarousel";
import { usePlans } from "../../dados/context/PlansContext"
import { useCarts } from "../../dados/context/CartContext"

function Home() {
  const history = useHistory();
  const [visible, setVisible] = useState({ status: false, text: "" });
  const [showModal3d, setShowModal3d] = useState(false);
  const [numVr, setNumVr] = useState(1)

  const { updateSignature, userIsAuthenticated, signature, setRouteToStack } =
  useAuth();

  const {addToCart} = useCarts();

  const changeVisible = (value) => {
    if (value === "off") {
      setVisible({
        status: false,
        text: value,
      });
    } else {
      setVisible({
        status: true,
        text: value,
      });
    }
  };

  const {
    lancamentos,
    getAllChannels,
    channels,
    getShowsByUserFav,
    showsSignatures,
    getAllChannelsFavoriteByUser
  } = useShows();
  const { plans } = usePlans();
  const userId = userIsAuthenticated() ? app.auth().currentUser?.uid : "";

  useEffect(() => {
    getAllChannels();
  }, []);

  useEffect(() => {
    getShowsByUserFav();
  }, []);

  useEffect(() => {
    userId && getAllChannelsFavoriteByUser(userId, false);
  }, []);

  function handlePlan3d() {

    let yearlyPlan = plans.filter(plan => plan.name === "Anual")

    // yearlyPlan[0].value = 19178 + (numVr * 3511)

    addToCart({
      ...yearlyPlan[0],
      signature_id: yearlyPlan[0].id,
      signature_value: yearlyPlan[0].value,
      num_vr: numVr
    });
    setRouteToStack("/");
    history.push("/cart");

  }

  return (
    <Layout page={0}>
      <>
        <Banner></Banner>
        <S.Container>
          <S.PwBanner
            src={logo}
            alt="Logotipo Palcoweb"
            onClick={() => history.push("/")}
          />
          <S.DescriptionContainer>
            <h2>Sua casa de espetáculos online</h2>

            <p>
              Aqui você pode assinar e acessar todo nosso conteúdo! Ou comprar apenas 
              seu espetáculo preferido.
            </p>

            <span>Assista onde quiser, cancela quando quiser.</span>

            <div>
              <button onClick={() => history.push('/Assinatura')}>Assine Já</button>
              <button onClick={() => setShowModal3d(true)}>Conhecer Promoção 3D</button>
            </div>

            <hr/>
          </S.DescriptionContainer>

          {lancamentos?.map((show) => (
            <Section key={show.id} title={show.name} filter>
              <SpectaclesCarousel
                spectacles={show.spectacle}
              >
              </SpectaclesCarousel>
            </Section>
          ))}

          <Section title="Assinaturas" filter>
            <SpectaclesCarousel
              spectacles={showsSignatures?.spectacle ? showsSignatures.spectacle : []}
            >
            </SpectaclesCarousel>
          </Section>

          <Section title="Canais" filter>
            <ChannelsCarousel
              spectacles={channels}
            >
            </ChannelsCarousel>
            <S.BtnNoStyle onClick={() => history.push('/canais')}>Ver Tudo</S.BtnNoStyle>
          </Section>

          <S.DescriptionContainer>
          <hr/>
          </S.DescriptionContainer>
          <S.DescriptionContainer style={{background:'black'}}>
            <p>Conheça</p>
            <S.PwBanner src={TvPw}></S.PwBanner>
            <p>Canal do PalcoWeb voltado para </p>

            <h2 style={{color:'#FDCC12'}}>
              Entrevistas com personalidades  
            </h2>
            <h2 style={{color:'#FDCC12'}}>
              do mundo cultural 
            </h2>

            <p>Desde roteiristas passando por produtores e atores.</p>

            <div>
              <S.BtnNoStyle onClick={() => history.push('/canalpw')} style={{color:"#fff", background:"transparent"}}>Saiba Mais</S.BtnNoStyle>
            </div>
          </S.DescriptionContainer>
          <S.DescriptionContainer>
          <hr/>
          </S.DescriptionContainer>
          <DuvidasFrequentes></DuvidasFrequentes>
        </S.Container>
      </>

    {showModal3d &&
    
      <S.PopapDiv>
        <S.PopapDiv1 >

          <span style={{cursor: "pointer", float: "right", marginRight: "-90%"}} onClick={() => setShowModal3d(false)} size="1.5">X</span>

          <S.Span style={{fontWeight: 'bold'}} cor="black" size="2">
            Promoção 3D 
          </S.Span>

          <S.Span cor="black" size="1.8" >
          
            Na compra da assinatura anual você ganha um óculos VR.
            
          </S.Span>

          <S.Span cor="black" size="1.8" >
          
            Após a compra você receberá o óculos VR para seu uso em regime de comodato ficando sob sua responsabilidade. Uso exclusivo para as peças em visualização VR do PalcoWeb. Ideal para smartphones de 4.7'' a 6.5''. O uso desse equipamento com óculos de grau depende da armação.
            
          </S.Span>


          {/* <S.Span cor="black" size="1.8">
            Deseja comprar mais óculos VR?
          </S.Span>

          <S.Span cor="black" size="1.8">
            Selecione o N° de óculos
          </S.Span>

          <select onChange={(e) => setNumVr(e.target.value)}>
            <option value={1}>1</option>
            <option value={2}>2</option>
            <option value={3}>3</option>
          </select> */}

          <div style={{display: "flex", justifyContent: "space-between"}}>
            
            {/* <S.PopapButton onClick={() => window.open('https://produto.mercadolivre.com.br/MLB-1688483109-oculos-realidade-virtual-3d-compativel-com-todos-smartphones-_JM')}>
              <S.Span size="1.5">Comprar Óculos VR</S.Span>
            </S.PopapButton> */}
            
            <S.PopapButton onClick={() => handlePlan3d()}>
              <S.Span size="1.5">Assine Já!</S.Span>
            </S.PopapButton>
            
          </div>

          <b>Não nos responsabilizamos pelo uso inadequado do produto.</b>

        </S.PopapDiv1>
      </S.PopapDiv>}

    </Layout>
  );
}

export default Home;
