import { createGlobalStyle } from "styled-components";

export const GlobalStyle = createGlobalStyle`

.form-control,
.form-control:focus,
.form-control:active {
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  border: 1px solid #fff;
  background: transparent !important;
  border-radius: 3px;
}

.form-control {
  box-shadow: none;
  background: transparent !important;
  /* font-weight: 600; */
  color: #fff !important;
  &::placeholder{
    color: #fff !important;
  }
}
.form-group label {
  font-size: 1rem !important;
  color: #fff !important;
  font-weight: 600;
}

.form-control:focus {
  outline: 0;
  box-shadow: none;
}

a,
div,
h1,
h2,
h3,
h4,
h5,
p,
span {
  text-shadow: none;
}

[type='button']:focus,
a:active,
a:focus,
a:visited,
button::-moz-focus-inner,
input[type='reset']::-moz-focus-inner,
input[type='button']::-moz-focus-inner,
input[type='submit']::-moz-focus-inner,
input[type='file'] > input[type='button']::-moz-focus-inner,
select::-moz-focus-inner {
  outline: 0;
}

input,
.form-control:focus,
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
  outline-width: 0;
  outline-color: transparent;
  box-shadow: none;
  outline-style: none;
}

textarea {
  resize: none;
  overflow-x: hidden;
}


.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus,
button,
button:active,
button:checked,
button:focus,
button:hover,
button:visited {
  outline: 0;
  outline-offset: 0;
}
.group{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 15px 10px;
}
.tooltip-error{
  position: absolute;
  top: 100%;
  z-index: 5;
  opacity: 0;
  display: none;
  max-width: 100%;
  padding: .25rem .5rem;
  margin-top: .1rem;
  font-size: .875rem;
  color: #fff;
  background-color: rgba(0,0,0,.9);
  border-radius: .25rem;
  transition: all 1s;

}
.tooltip-error-static{
  position: static;
  display: none;
}
.show{
  opacity: 1;//final
  color: red;
  display: inline-block;
}

.show-top{
    transform: translateY(-45px);//final
  }
  .show-bottom{
    transform: translateY(45px);//final
  }
  .show-left{
    transform: translateX(-300px);//final
  }

  .show-right{
    transform: translateX(560px);//final
  }

.bootstrap-select .dropdown-toggle:focus {
  outline: 0;
  outline-offset: 0;
}

.dropdown-menu > li > a:active,
.dropdown-menu > li > a:focus,
.dropdown-menu > li > a:hover,
.dropdown-menu > li > a:visited {
  outline: 0;
}

a:focus,
input:focus {
  border-color: #fff;
  outline: none;
}
`;
