import * as S from "../styles";
import React, { useEffect } from "react";

function HandleSubCategories({setShowList, showsByCategory, subCategories, setSelectedSubCategory}) {

    function filterShowsBySubCategory(subCategory) {

      const filteredShows = showsByCategory.filter(item => {

        return item.categoryItens.find(sub => {
          return subCategory.id === sub.id
        });
      })
      
      setShowList(filteredShows)
        
    }

  
    return <>
    {
      subCategories?.map((subCategory, index) => (
        <S.FiltroSubItem key={index}>
          <div className="checkbox">
            <input
              type="radio"
              id={index}
              className="checkbox"
              name="chk"
              value={subCategory.id}
              onChange={() => {
                setSelectedSubCategory(subCategory)
                filterShowsBySubCategory(subCategory)
              }}
            />
            <label htmlFor={index}>{subCategory.name}</label>
          </div>
        </S.FiltroSubItem>
    ))}</>
}

export default HandleSubCategories;