import axios from "axios";
import { baseUrl, stripeUrl } from "./consts";

const api = axios.create({
  baseURL: baseUrl,
});

api.interceptors.request.use((config) => {
  const token = localStorage.getItem("@PermissionYT:token");
  if (token) {
    config.headers.Authorization = `Bearer ${token}`;
  }

  return config;
});

export async function cancelPayment(paymentID){
  await api.post(baseUrl + `/cancel`, {
    id: paymentID
  })
  .then((response) => {
    console.log(response)
  }).catch(error => {
    alert("Erro");
  })
}

export function refreshToken() {
  api.interceptors.response.use(null, (error) => {
    const { response } = error;
    if (!response) {
      // network error
      console.error(error);
      return;
    }

    if ([401].includes(response.status)) {
      // auto logout if 401 or 403 response returned from api
      if (localStorage.getItem("@PermissionYT:refreshToken")) {
        console.log(localStorage.getItem("@PermissionYT:refreshToken"), 'TOKEN');
        api
          .post("/auth/refresh_token", {
            id: JSON.parse(
              JSON.stringify(localStorage.getItem("@PermissionYT:refreshToken"))
            ),
          })
          .then((tokenapi) => {
            localStorage.setItem(
              "@PermissionYT:token",
              tokenapi?.data?.body?.accessToken
            );
          })
          .catch((err) => {
            if (err.response) {
              if([401].includes(err.response.status)){
                localStorage.clear();
                window.location.reload();
              }
            }
          });
      }
    }
    if ([402].includes(response.status)) {
      localStorage.clear();
      window.location.reload();
    }

    // if ([403].includes(response.status)) {
    //   console.log("cheguei");

    //   return;
    // }
    throw error;
    // const errorMessage = response.data?.message || response.statusText;
    // console.error("ERROR: erro", errorMessage);
  });
}

export default api;
