import React, { useEffect } from "react";
import * as S from "./styles";
import app from "../../auth/config";
import { useHistory, useLocation } from "react-router-dom";
import "./style.css";
import firebase from "firebase";
import Layout from "../../layout";
import { useAuth } from "../../auth/auth";
import SpinnerCustom from "../../components/SpinnerCustom";
import { toast, ToastContainer } from "react-toastify";
import { optionsToast } from "../../dados/context/ShowsContext";

function Login() {
  const {
    setUser,
    setLoading,
    error,
    token,
    setError,
    addNewUserToFirestore,
    loading,
  } = useAuth();
  const history = useHistory();
  const location = useLocation();

  const handleLogin = async (event) => {
    event.preventDefault();
    const { email, password } = event.target.elements;

    setLoading(true);
    if (!(email.value && password.value)) {
      setLoading(false);
      setError(true);
      toast.error(`digite email e senha!`, optionsToast);
      return;
    }
    app
      .auth()
      .signInWithEmailAndPassword(email.value, password.value)
      .then((user) => {
        setUser(user);
      })
      .catch((error) => {
        if (error.code) {
          setLoading(false);
          setError(true);
          toast.error(`Ooops, algo deu errado`, optionsToast);
        }
      });
  };
  useEffect(() => {
    if (error && !token) {
      setLoading(false);
      toast.error("Usuário ou senha inválidos", optionsToast);
    } else if (token) {
      const goSignature = localStorage.getItem("stack:route");
      location.state?.redirect && goSignature
        ? history.push(goSignature)
        : history.push("/");
    }
  }, [error, token]);


  async function loginGoogle() {
    var provider = await new firebase.auth.GoogleAuthProvider();
    firebase
      .auth()
      .signInWithPopup(provider)
      .then((user) => {
        const docRef = app
          .firestore()
          .collection("users")
          .doc(app.auth().currentUser.uid);
        docRef
          .get()
          .then((doc) => {
            if (doc.exists) {              
              setUser(user);
              return user;
            } else {
              
              addNewUserToFirestore(user);
              setUser(user);
            }
          })
          .catch((error) => {
            if (error.response) {
              console.error('Checking if customer exists failed" ' + error);
              setError(true);
              toast.error(`Ooops, algo deu errado, ${error.message}`, optionsToast);
            }
          });
      })
      .catch((error) => {
        if (error.response) {
          setError(true);
          toast.error(`Ooops, algo deu errado, ${error.message}`, optionsToast);
        }
      });
  }
  return (
    <Layout>
      <ToastContainer/>
      <S.Container>
        <S.ContainerContent>
          <S.ContainerItem className="pt-5">
            <S.Span size="25">Minha conta</S.Span>
          </S.ContainerItem>
          <form onSubmit={handleLogin}>
            <S.ContainerItem>
              <input
                className="form-control"
                name="email"
                type="email"
                placeholder="Email"
              />
            </S.ContainerItem>
            <S.ContainerItem>
              <input
                className="form-control"
                name="password"
                type="password"
                placeholder="Senha"
              />
            </S.ContainerItem>
            <S.ContainerItem className="pt-3">
              <S.Entrar className="btn m-2 " disabled={loading}>
                {loading ? <SpinnerCustom variant="md" /> : "Entrar"}
              </S.Entrar>
              <S.Cadastro
                className="btn m-2"
                onClick={() => history.push(`/Cadastro`)}
              >
                {" "}
                Cadastre-se
              </S.Cadastro>
            </S.ContainerItem>
          </form>

          <S.Span
            size="15"
            className="p-3 ponte"
            onClick={() => history.push("/EsqueciSenha")}
          >
            Esqueceu a senha?
          </S.Span>

          <S.Google onClick={loginGoogle}>
            <i className="bi bi-google"></i> Entrar com Google
          </S.Google>
        </S.ContainerContent>
      </S.Container>
    </Layout>
  );
}

export default Login;
