import React, { useState, useEffect } from "react";
import * as S from "./styles";
import Lottie from "react-lottie";
import success from "./lottie/success.json";
import falide from "./lottie/falide.json";
import { Cupoms } from "./cupom";

export default function PopupCupom(props) {
  const [cupomSelect, setCupomSelect] = useState(null);

  const defaultOptionssuccess = {
    loop: false,
    autoplay: true,
    animationData: success,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  const defaultOptionsfalide = {
    loop: false,
    autoplay: true,
    animationData: falide,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };

  useEffect(() => {
    if (props.tipo === "espetaculo") {
      if (Cupoms && props.cupom) {
        

        Cupoms.map((cupom) => {
          console.log(cupom.cupom)
          if (cupom.cupom === props.cupom && cupom.tipo === "espetaculo") {
            setCupomSelect(cupom);
            props.returnValue(true);
          }
          return 0;
        });
      }
    }
    
    if (props.tipo === "assinatura") {
      if (Cupoms && props.cupom) {
        if (props.cupom === "#ParmêNoTeatro" && props.plano?.name === "Anual") {
          
          Cupoms.map((cupom) => {
            if (cupom.cupom === props.cupom && cupom.tipo === "assinatura") {
              setCupomSelect(cupom);
              props.returnValue(true);

            }
            return 0;
          });
        } else {
          
          Cupoms.map((cupom) => {
            if (
              cupom.cupom === props.cupom &&
              cupom.tipo === "assinatura" &&
              cupom.cupom !== "#ParmêNoTeatro"
            ) {
              setCupomSelect(cupom);
            }
            return 0;
          });
        }
      }
    }
  }, [props]);

  return (
    <>
      {props.cupom && (
        <S.PopapDiv style={{ height: "100%" }}>
          <S.PopapDiv1 style={{ height: "100%" }}>
            {cupomSelect ? (
              <S.Span cor="black" size="1.8" className="mb-3 mt-3">
                <h1> Cupom válido</h1>
              </S.Span>
            ) : (
              <S.Span cor="black" size="1.8" className="mb-3 mt-3">
                <h1>Cupom inválido </h1>
              </S.Span>
            )}
            {cupomSelect && (
              <S.Span cor="black" size="1" className="">
                <h2>{cupomSelect.cupom}</h2>
              </S.Span>
            )}
            {cupomSelect && (
              <S.Span cor="black" size="1" className="">
                {cupomSelect.descricao}
              </S.Span>
            )}
            {cupomSelect ? (
              <Lottie
                height={"70%"}
                width={"70%"}
                options={defaultOptionssuccess}
              ></Lottie>
            ) : (
              <Lottie
                height={"auto"}
                width={"auto"}
                options={defaultOptionsfalide}
              ></Lottie>
            )}
            <S.Span cor="black" size="0.8" className="">
              <h5>{cupomSelect?.detalhes}</h5>
            </S.Span>
            <S.PopapDivButton>
              <S.PopapButton
                onClick={() => {
                  props.closePopup(false)
                }}
              >
                <S.Span size="1.5">continuar</S.Span>
              </S.PopapButton>
            </S.PopapDivButton>
          </S.PopapDiv1>
        </S.PopapDiv>
      )}
    </>
  );
}
