import ViaCep from 'react-via-cep';
import React from "react"
import * as S from "./styles.js"

const GetAddressByCep = ({cep, setCep, address, setAddress, children}) => {

  
  return <ViaCep cep={cep} lazy>
    { ({ data, loading, error, fetch }) => {
        
        if (error) {
            setAddress("CEP inválido")
        }
        if (data) {
            setAddress(data.logradouro ? data.logradouro + ", " + data.bairro + ", " + data.localidade + " - " + data.uf : "CEP inválido")
        }

        return <S.Row>
            <S.Input 
                className="form-control"
                placeholder="CEP"
                value={cep}
                onChange={(e) => setCep(e.target.value)}
                
            />
            <button style={{color:'#000', background:'#fdcc12', padding:'5px 10px', borderRadius:'6px', width: "25%"}} onClick={fetch}>Pesquisar</button>
        </S.Row>
    }}
    </ViaCep>

}

export default GetAddressByCep