import React, { useEffect, useState, createContext, useContext } from "react";
import PaymentService from "../../Connections/payment";
import { convertToNumber, isValidCpf } from "../../utils/utils";
import valid from "card-validator";

const Payments = createContext({});

export function PaymentsProvider(props) {
  const [paymentList, setPayment] = useState({});
  const [listaPlanos] = useState({ teste: "ok" });
  const [error, setError] = useState({});
  const [submissionError, setSubmissionError] = useState({});
  const [status, setStatus] = useState(null);

  useEffect(() => {
    getStorage();
  }, []);

  useEffect(() => {
    if (submissionError) {
      setSubmissionError({ ...submissionError, ...error });
    } else {
      setSubmissionError(error);
    }
  }, [error]);

  async function paySignature(payment, cb) {
    try {
      const response = await PaymentService.paySignature(payment);
      cb(response, null);
    } catch (error) {
      cb(null, error.response);
    }
  }

  async function paySpectacle(payment, cb) {
    try {
      // const response = await PaymentService.generatePublicKey();
      //TODO chamar funcao para enviar para a api
      const response = await PaymentService.paySpectacle(payment);
      cb(response, null);
    } catch (error) {
      cb(null, error.response);
    }
  }

  async function payFree(payment, cb) {
    const { user, plan_id } = payment;

    try {
      // const response = await PaymentService.generatePublicKey();
      //TODO chamar funcao para enviar para a api
      const data = { user, plan_id };
      await PaymentService.payPlanFree(data);
    } catch (error) {
      cb(error.response);
    }
  }

  function getStorage() {
    if (localStorage.getItem("Payment:item")) {
      setPayment(JSON.parse(localStorage.getItem("Payment:item")));
    }
  }

  function addToPayment(item) {
    setPayment(item);
    localStorage.setItem("Payment:item", JSON.stringify(item));
  }

  function removeToPayment() {
    setPayment({});
    localStorage.removeItem("Payment:item");
  }

  function validate(field, typeValidation) {
    let errors = {};
    const { name, value } = field.target;
    if (typeValidation["required"]) {
      if (!value) {
        errors[name] = "Campo obrigatório";
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["max"]) {
      if (value.length > typeValidation.max) {
        errors[name] = `O tamanho máximo é de ${typeValidation.max} caracteres`;
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["min"]) {
      if (value.length < typeValidation.min) {
        errors[name] = `O tamanho mínimo é de ${typeValidation.min} caracteres`;
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["cpf"]) {
      if (!isValidCpf(convertToNumber(value))) {
        errors[name] = `Insira um cpf válido`;
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["cardNumber"]) {
      if (!valid.number(convertToNumber(value)).isValid) {
        errors[name] = `Insira um número de cartão válido`;
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["cvv"]) {
      if (!valid.cvv(convertToNumber(value)).isValid) {
        errors[name] = `Código de segurança inválido`;
      } else {
        error[name] = "isValid";
      }
    }

    if (typeValidation["expDate"]) {
      if (!valid.expirationDate(convertToNumber(value)).isValid) {
        errors[name] = `Data de expiração inválida`;
      } else {
        error[name] = "isValid";
      }
    }
    console.log(errors, "error");
    setError({ ...error, ...errors });
  }

  return (
    <Payments.Provider
      value={{
        addToPayment,
        removeToPayment,
        paymentList,
        status,
        setStatus,
        paySignature,
        paySpectacle,
        payFree,
        listaPlanos,
        validate,
        submissionError,
        error,
      }}
    >
      {props.children}
    </Payments.Provider>
  );
}

export const usePayments = () => useContext(Payments);
