import { breakNumber, convertToNumber, currency } from "../utils"

export const formatPlans = (items) => {
    const plans = items.map(item => ({
        ...item,
        value: convertToNumber(item.value),
        breakValue: breakNumber(currency(item.value))
    }))
    return plans
}

export const formatPlan = (item) => {
    const plans = {
        ...item,
        value: convertToNumber(item.value),
    }
    return plans
}