import apiPs from "./api-config-pagseguro";
import api from "./apis";

class PaymentService {

  static async generatePublicKey() {

    let path = "/public-keys";

    return await apiPs.post(path, {
      "type": "card"
    });
  }

  static async paySignature(payment) {
    const {
      id,
      userId,
      itemId,
      value
    }= payment;
    let path = `/payment/stripe/signature`;
        
    const dataPayment = {user:userId, plan_id:itemId,payment_method:id, amount: value}
    return await api.post(path, dataPayment);
  }

  // static async cancelSignature(payment) {
  //   let path = `/payment/stripe/signature`;
  //   const { user, plan_id } = payment;

  //   const dataPayment = { user, plan_id };
  //   await api.post(path, dataPayment);
  // }

  static async paySpectacle(payment) {
    const {
      id,
      userId,
      itemId,
      value
    } = payment;
    let path = `/payment/stripe/on-demand`;
        
    const dataPayment = {user:userId, spectacle_id:itemId,payment_method:id, amount: value}
    return await api.post(path, dataPayment);
  }

  static async payPlanFree(payment) {
    const {user, plan_id} = payment;
    let path = `/plans/experiment`;
        
    const dataPayment = {user_id:user, plan_id}
    return await api.post(path, dataPayment);
  }

}

export default PaymentService;
