import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { useHistory } from "react-router";
import { useAuth } from "../../auth/auth";
import { useCarts } from "../../dados/context/CartContext";
import { useShows } from "../../dados/context/ShowsContext";

import { Button } from "./styles";

function ButtonContinuar({
  title,
  toPlan,
  userIsSignature,
  userIsAuth,
  spectacle,
  page,
  freeVideoID,
  freeVideoID2,
  showModal3d,
  setShowModal3d
}) {
  const history = useHistory();
  const cart = useCarts();
  const { setRouteToStack } = useAuth();
  const { adquiridos, getShowsByUser, getShowWithLink } =
    useShows();
  const [titleBtnSignature, setTitleBtnSignature] = useState()
  //toPlan = 'signature' || 'onDemand' referente ao espetáculo
  //user   = 'isSignature' || 'onDemand'
  //spectacle = obj spectacle propriedades para o link, vem da rota privada
  useEffect(()=>{
    if(toPlan === "signature"){
      userIsSignature ? setTitleBtnSignature('Assistir'): setTitleBtnSignature('Assine Já')
    }else{
      if(!userIsAuth){
        setTitleBtnSignature('Comprar')
      }else{
        getShowsByUser();
        //fazer um find e verificar na rota de filmes adquiridos se esse espetáculo está lá
        const hasSpectacle = adquiridos.find(
          (show) => show.id === spectacle.id
        );
        hasSpectacle ? setTitleBtnSignature('Assistir'): setTitleBtnSignature('Comprar')
      }
    }
  },[toPlan])
  
  const handleClick = () => {
    if (toPlan === "signature") {
      // assinatura
      if (!userIsSignature) {
        history.push(`/Assinatura`);
      } else if (userIsSignature) {
        getShowWithLink(spectacle.id, (error, response) => {
          response &&
            history.push(
              `/playespetaculo/${response.videoID}/${response.videoID2}/${response.id}`
            );
        });
      } else {
        history.push(`/Assinatura`);
      }
    } else if (toPlan === "onDemand" && spectacle?.title?.includes("3d")) { 
      document.getElementById('root').scrollTo(0, { behavior: "smooth" })
      setShowModal3d(true)
      console.log('3d')
    }else if (toPlan === "onDemand") {
      console.log("onDemand");
      //onDemand
      if (!userIsAuth) {
        page === "cardezao" &&
          cart.addToCart({
            id: spectacle.id,
            valorPreco: spectacle.price_on_demand,
            link: spectacle.link,
            direcao: spectacle.direction,
            producao: spectacle.production,
            elenco: spectacle.cast,
            artista: spectacle.producer_id,
            sinopsiaEsp: spectacle.synopsis,
            descricaoClassifica: spectacle.ageRatingDescription,
            classificacao: spectacle.ageRating,
            thumbnail: spectacle.thumbnail,
            tituloEsp: spectacle.title,
            artistaEsp: spectacle.producer_id,
            categoria: spectacle.producer_id,
            videoID: spectacle.videoID,
            videoID2: spectacle.videoID2,
            trailher: spectacle.trailer,
          });
        page === "cardezao" && setRouteToStack(`/cart`);
        page === "cardezao" && history.push(`/cart`);
      } else if (userIsAuth) {
        getShowsByUser();
        //fazer um find e verificar na rota de filmes adquiridos se esse espetáculo está lá
        const hasSpectacle = adquiridos.find(
          (show) => show.id === spectacle.id
        );
        if (hasSpectacle) {
          // pegar parametros da rota privada
          getShowWithLink(spectacle.id, (error, response) => {
            response &&
              history.push(
                `/playespetaculo/${response.videoID}/${response.videoID2}/${response.id}`
              );
          });
        } else {
          cart.addToCart({
            id: spectacle.id,
            valorPreco: spectacle.price_on_demand,
            link: spectacle.link,
            direcao: spectacle.direction,
            producao: spectacle.production,
            elenco: spectacle.cast,
            artista: spectacle.producer_id,
            sinopsiaEsp: spectacle.synopsis,
            descricaoClassifica: spectacle.ageRatingDescription,
            classificacao: spectacle.ageRating,
            thumbnail: spectacle.thumbnail,
            tituloEsp: spectacle.title,
            artistaEsp: spectacle.producer_id,
            categoria: spectacle.producer_id,
            videoID: spectacle.videoID,
            videoID2: spectacle.videoID2,
            trailher: spectacle.trailer,
          });
          setRouteToStack(`/cart`);
          history.push(`/cart`);
        }
      }
    } else {
      history.push(
        `/playcanalpw/${freeVideoID}/${freeVideoID2}`
      );
    }

    //Chamar a api com a rota privada
  };
  return (
    <Button className="btn btn-warning" onClick={() => handleClick()}>
      {titleBtnSignature ||   title}
    </Button>
  );
}

export default ButtonContinuar;
