import axios from "axios";
import api from "./apis";
import { baseUrl } from "./consts";

class ShowsService {



  static async loadShows() {
    let path = "/spectacle/list";

    return await api.get(path);
  }

  static async getShowsByUser() {
    let path = `/user/on-demand`;

    return await api.get(path);
  }

  static async getShowsByUserFav() {
    let path = `/user/favorite-spectacle`;

    return await api.get(path);
  }

  static async getShowById(id) {
    let path = `/spectacle?id=${id}`;

    return await api.get(path);
  }

  static async getShowLink(id) {
    let path = `spectacle/view?id=${id}`;

    return await api.get(path);
  }

  static async getCategories() {
    let path = `/category/list`;

    return await api.get(path);
  }

  static async getSubCategories(value) {
    let path = `/category/${value}/itens`;

    return await api.get(path);
  }

  static async getShowsByCategory(value) {
    let path = `/spectacle/list?category=${value}`;
    
    return await api.get(path);
  }

  static async getShowsSignature(value) {
    let path = `/spectacle/list?isActiveSignature=${value}`;

    return await api.get(path);
  }

  static async getShowsOnDemand(value) {
    let path = `/spectacle/list?isActiveOnDemand=${value}`;

    return await api.get(path);
  }

  static async getShowsTrending(value) {
    let path = `/spectacle/list?trending=${value}`;

    return await api.get(path);
  }

  static async setShowLike(user_id, spectacle_id) {
    let path = `/spectacle/like`;

    return await api.post(path, {user_id, spectacle_id});
  }

  static async setShowUnLike(user_id, spectacle_id) {
    let path = `/spectacle/dislike/${spectacle_id}`;

    return await api.delete(path, {data:{user_id}});
  }

  static async loadLista() {
    let path = "http://localhost:3333/lista";

    return await axios.get(path);
  }

  async getContent(id) {
    let url = "";

    id
      ? (url = `${baseUrl}/contents?content_id=${id}`)
      : (url = `${baseUrl}/contents`);

    return await axios.get(url, await this.user.headerConfig());
  }
}

export default ShowsService;
