import React, { useEffect } from "react";
import { useParams } from "react-router-dom";
import Player from "@vimeo/player";
import * as S from "./styles";
import { useHistory, useLocation } from "react-router-dom";
import { useShows } from "../../dados/context/ShowsContext";
import { useAuth } from "../../auth/auth";
import app from "../../auth/config";


export default function ComponenteVideo() {
  const { watchShow } = useShows();
  const history = useHistory();
  const params = useParams("/playcanalpw/:id1/:id2/");
  const { id1, id2 } = params;
  const { userIsAuthenticated } = useAuth();
  const userId = userIsAuthenticated() ? app.auth().currentUser.uid : "";

  let location = useLocation();

  useEffect(() => {
    setTimeout(() => {
      watchShow(userId, id1);
    }, 10000);

    //cria o player
    if (location.pathname.substr(0, 15) === "/playespetaculo") {
      var options = {
        width: 640,
        loop: true,
      };

      var iframe = document.querySelector("iframe");

      var player = new Player(iframe, options);

      setInterval(() => {
        player.on("playing", function (data) {
          player
            .getCurrentTime()
            .then(function (seconds) {
              localStorage.setItem(
                `@App:${id1}/${id2}`,
                JSON.stringify(seconds)
              );
              // seconds = the current playback position
            })
            .catch(function (error) {
              console.log(error);
              // an error occurred
            });
        });

        player.on("ended", function (data) {
          history.push("/");
        });
      }, 500);

      const storagedUser = localStorage.getItem(`@App:${id1}/${id2}`);

      if (storagedUser) {
        player.ready().then(function () {
          player
            .setCurrentTime(JSON.parse(storagedUser))
            .then(function (seconds) {
              // seconds = the actual time that the player seeked to
              console.log(seconds);
            })
            .catch(function (error) {
              switch (error.name) {
                case "RangeError":
                  // the time was less than 0 or greater than the video’s duration
                  break;

                default:
                  // some other error occurred
                  break;
              }
            });
        });
      }
    }
  }, []);

  return (
    <div style={{ width: "100vw", display: "flex", padding: 0 }}>
      <S.div>
        <S.Span
          size="35"
          style={{ cursor: "pointer" }}
          onClick={() => history.push("/")}
        >
          <i className="bi bi-arrow-left"></i>
        </S.Span>

      </S.div>

      <iframe
        src={`https://player.vimeo.com/video/${id1}?h=${id2}`}
        frameBorder="0"
        allow="autoplay; fullscreen; picture-in-picture"
        allowfullscreen
        title="Cad&amp;ecirc;.mp4"
        style={{
          position: "absolute",
          marginTop: "0px",
          padding: 0,
          width: "100vw",
          height: "100vh",
        }}
      ></iframe>
    </div>
  );
}
