import styled from "styled-components";
import { SCREEN_SIZES } from "../../utils/screen";

export const Container = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top:100px;
  flex-direction: column;
  color: white;
`;


export const Carrinho = styled.div`
display: flex;
width: 100%;
height: 200px;

flex-direction: row;
align-items: center;
justify-content: center;
`

export const CarrinhoItem = styled.div`
  display: flex;
  height: auto;
  width: 10%;
  
  flex-direction: column;
`

export const X = styled.div`
    display: flex;
    width: 2%;
    height: 150px;
   
`

export const Span = styled.span`
    color: ${props => props.cor || 'white'};
    font-size: 10px;
    font-size:${props => props.size + "px"};
`
export const Info = styled.div`
    display: flex;
    flex-direction: column;
   
    width: 30%;
    padding: 10px;
`
export const ButtonContinuar = styled.button`
background-color: #FDCC12;
color: black;
text-transform: uppercase;
width: 100%;
:hover{
    background-color: #FDCC12;
}`



//Card
///tudo sobre card


export const CardTest = styled.div`

  width: 250px;
  height: 450px;
  color: white;

  @media (max-width: ${SCREEN_SIZES.sm}) {
    width: 100vw;
  }


`

export const ItemTest = styled.div`
  width: 250px;
  height: 400px;
  background-image: url(${props => props.urlimg});
  background-color: blue;

  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 20px;
 
`



export const CardeZao = styled.div`

background-color: black;
display: flex;
position: absolute;
z-index: 9;
flex-direction: row;
color: white;
box-shadow: 5px 5px 5px  5px rgba(0,0,0,0.2);

width: 100%;
height: 800px;
`

export const CardeZaoConteudo = styled.div`
display: flex;
flex-direction: column;
 justify-content: space-between;

 align-items: center;

`
export const CardeZaoInformacos = styled.div`
display: flex;
flex-direction: column;
 justify-content: space-around;


`
export const Img = styled.img`
  
  width: 250px;
  height: 400px;
  box-shadow: 5px 5px 5px  5px rgba(0,0,0,0.2);

  @media (max-width: ${SCREEN_SIZES.sm}) {    
    
    width: 100vw;
    height: 100%;
  }
`

export const BtnVermelho = styled.button`

  width: 50px;
  height: 50px;
  position: relative;
  top:-325px;

`



export const BtnVerde = styled.a`
align-items: center;
margin: auto;

`
export const P = styled.p`

`
export const InfoCard = styled.div`
position: fixed;
width: 250px;
height: 400px;
display: flex;
flex-direction: column;
padding: 15px;
justify-content: space-between;
@media (max-width: ${SCREEN_SIZES.sm}) {
    
  width: 100%;
  height: 100%;
}
background-color: rgba(0, 0, 0, 0.5) ;
opacity: 0;
:hover{
 opacity: 1;
}
`

export const InfoCardConteudo = styled.div`
  
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 5px;
  
`

